import React from 'react';
import textContext from '../../textContext';
import { useApp } from '../../context/AppContext';
import { anchor, terra } from '../../assets/images/icons';
import { useToken } from '../../context/TokenContext';
import { useDelta } from '../../context/DeltaContext';
import { useTheme } from '../../context/ThemeContext';
import Asset from '../../pages/Asset';
import { useWallet } from '@terra-money/wallet-provider';
import Tooltip from '@mui/material/Tooltip';
import { Help as HelpIcon, Info as InfoIcon } from '@mui/icons-material';
import { useQueries } from '../../context/QueriesContext';
import useUtils from '../../hooks/useUtils';
import { tokenBalanceQuery } from '../../gql/hive/hiveGQL';

export default function Assets(props) {
  const {
    exchange,
    anchorApy,
    market,
    investData,
    investPlus,
    investPlusLookup,
    investDataLookup,
  } = useToken();
  const { network } = useWallet();
  const { lcd, addressProvider, hiveClient } = useQueries();
  const { anchorInvestment } = useDelta();
  const [openInvest, setOpenInvest] = React.useState(false);
  const [openWithdraw, setOpenWithdraw] = React.useState(false);
  const { holderAddress, language } = useApp();
  const text = textContext[language].portfolio.asset;
  const { assetImg } = useTheme();
  const { numberParse } = useUtils();
  const [marketHour, setMarket] = React.useState(false);
  const [assetData, setAssetData] = React.useState(false);

  const handleClose = () => {
    setOpenInvest(false);
    setOpenWithdraw(false);
  };

  const handleInvest = (inv) => {
    inv === 'invest' ? setOpenInvest(true) : setOpenWithdraw(true);
  };

  const getTime = (t) => {
    let event = new Date(t / 1e6);
    let months = [
      'Jan',
      'Feb',
      'Mar',
      'Apr',
      'May',
      'Jun',
      'Jul',
      'Aug',
      'Sep',
      'Oct',
      'Nov',
      'Dec',
    ];

    let month = months[event.getMonth()];
    let date = event.getDate();
    let year = event.getFullYear();
    let time = event.toLocaleTimeString('en-us');

    return `${time}, ${month} ${date}, ${year}`;
  };

  React.useEffect(() => {
    if (!!lcd && props.address && !props.anchor && investPlus.length > 0) {
      setAssetData(investPlus[investPlusLookup[props.address]]);
    } else if (props.anchor) {
      const getBalance = async () => {
        const query = tokenBalanceQuery(
          addressProvider.aTerra(),
          holderAddress
        );
        try {
          const [res] = await hiveClient.setQuery(query).request();
          return res;
        } catch (e) {
          console.error(e);
        }
      };
      getBalance().then((d) => {
        setAssetData({
          amount: d.balance,
          address: addressProvider.aTerra(),
          network: addressProvider.data.mmMarket,
          chain: 'terra',
          support: ['ust'],
          ticker: 'anc',
          title: 'Anchor Protocol',
          apy: market.markets[0].APY,
        });
      });
    }
  }, [
    props.address,
    investPlus,
    investPlusLookup,
    network,
    holderAddress,
    props.anchor,
    market,
    lcd,
    addressProvider,
    hiveClient,
  ]);

  React.useEffect(() => {
    if (!!props.anchor && investData[investDataLookup[props.address]]) {
      // Special case Anchor protocol: market hour is not applicable.
      if (investData.title === 'Anchor Protocol') {
        setMarket(true);
      }
    } else if (
      !props.anchor &&
      investPlusLookup[props.address] !== undefined &&
      !!investPlus[investPlusLookup[props.address]] &&
      !!investPlus[investPlusLookup[props.address]].action
    ) {
      setMarket(true);
    } else {
      setMarket(false);
    }
  }, [
    props.anchor,
    props.address,
    investData,
    investDataLookup,
    investPlus,
    investPlusLookup,
  ]); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <React.Fragment>
      <div className="investAsset">
        <div className="assetTop">
          <div className="flex">
            <img
              src={props.anchor ? anchor : assetImg(props.name)}
              alt="delta-neutral ust"
            />
            <div>
              {props.anchor ? (
                <span className="flex">
                  <h3>Anchor Protocol</h3>
                </span>
              ) : (
                <span className="flex">
                  <h3>Delta-neutral-{props.name && props.name}</h3>
                  <p className="fontOrange">
                    <span>original</span>
                  </p>
                </span>
              )}
              <p>Terra</p>
            </div>
          </div>
          {!props.anchor && (
            <p style={{ color: '#B1B5C3' }}>
              {text.time} {props.time && getTime(props.time)}
            </p>
          )}
        </div>
        <div className="assetBottom">
          <div>
            <span className="textUpper">
              {text.realTimeApr}
              <Tooltip
                title={text.aprHelperText}
                componentsProps={{
                  tooltip: {
                    sx: {
                      borderRadius: '12px',
                      opacity: 1,
                      border: 1,
                      borderColor: 'borderColor',
                    },
                  },
                }}
              >
                <InfoIcon
                  sx={{
                    width: '0.6em',
                    height: 'auto',
                    position: 'relative',
                    top: '0.08em',
                    left: '0.1em',
                    cursor: 'help',
                  }}
                />
              </Tooltip>
            </span>
            {props.anchor ? (
              <h3>{numberParse(anchorApy * 100)}%</h3>
            ) : (
              <>
                <h3>
                  {investPlus &&
                  investPlusLookup &&
                  investPlus[investPlusLookup[props.address]] &&
                  investPlus[investPlusLookup[props.address]].APR
                    ? `${numberParse(
                        investPlus[investPlusLookup[props.address]].APR * 100
                      )}%`
                    : '--'}
                </h3>
              </>
            )}
          </div>
          <div className="flex">
            {!props.anchor && !marketHour && (
              <div className="helpClass">
                <Tooltip
                  title={text.helperText}
                  componentsProps={{
                    tooltip: {
                      sx: {
                        borderRadius: '12px',
                        opacity: 1,
                        border: 1,
                        borderColor: 'borderColor',
                      },
                    },
                  }}
                >
                  <HelpIcon />
                </Tooltip>
              </div>
            )}
            <button
              disabled={!props.anchor && !marketHour}
              className={
                !props.anchor && !marketHour ? 'disabled button' : 'button'
              }
              onClick={() => handleInvest('withdraw')}
            >
              {text.withdraw}
            </button>
            <button
              disabled={!props.anchor}
              className={
                !props.anchor
                  ? 'disabled buttonColor'
                  : 'buttonColor'
              }
              onClick={() => handleInvest('invest')}
            >
              {text.invest}
            </button>
          </div>
        </div>
      </div>
      {props.anchor && openInvest && (
        <Asset
          invest
          data={assetData}
          img1={terra}
          img2={anchor}
          token={props.address}
          close={handleClose}
        />
      )}
      {props.anchor && openWithdraw && (
        <Asset
          withdrawValue={numberParse(
            anchorInvestment.value * exchange.exchange_rate,
            'small'
          )}
          data={assetData}
          img1={terra}
          img2={anchor}
          token={props.address}
          close={handleClose}
        />
      )}
      {props.address && openInvest && (
        <Asset
          plus
          invest
          data={assetData}
          img1={terra}
          img2={anchor}
          token={props.address}
          close={handleClose}
        />
      )}
      {props.address && openWithdraw && (
        <Asset
          plus
          currentValue={props.value}
          data={assetData}
          img1={terra}
          img2={anchor}
          token={props.address}
          close={handleClose}
          position={props.position}
        />
      )}
    </React.Fragment>
  );
}
