import useUtils from '../../hooks/useUtils';
import { useApp } from '../../context/AppContext';
import textContext from '../../textContext';

const AssetBreakdown = (props) => {
  const { language } = useApp();
  const { numberParse } = useUtils();
  const investText = textContext[language].investment.invest;
  const tableAssetsText = investText.tableAssets;
  const tooltipText = tableAssetsText.tooltip;
  const { collateralRatio, breakdown } = props;
  return (
    <table
      className={`assetBreakdown ${!!collateralRatio ? 'investAccordion' : ''}`}
    >
      <tbody>
        <tr>
          <td className="semiBold">
            {!!collateralRatio
              ? tooltipText.modalTitle
              : tooltipText.toolTipTitle}
          </td>
          <td></td>
        </tr>
        <tr>
          <td className="textSecondary">
            {tooltipText.collateral}
            {!collateralRatio && <sup>1</sup>}
          </td>
          <td>{`${numberParse(
            (collateralRatio
              ? breakdown.collateralApr(collateralRatio)
              : breakdown.collateralApr()) * 100
          )}%`}</td>
        </tr>
        <tr>
          <td className="textSecondary">{tooltipText.short}</td>
          <td>{`${numberParse(
            (collateralRatio
              ? breakdown.shortFarmApr(collateralRatio)
              : breakdown.shortFarmApr()) * 100
          )}%`}</td>
        </tr>
        <tr>
          <td>
            <span className="semiBold">{tooltipText.firstApr}</span>
          </td>
          <td className="successColor">
            {`${numberParse(
              (collateralRatio
                ? breakdown.collateralApr(collateralRatio) +
                  breakdown.shortFarmApr(collateralRatio)
                : breakdown.collateralApr() + breakdown.shortFarmApr()) * 100
            )}%`}
          </td>
        </tr>
        <tr>
          <td className="textSecondary">
            {tooltipText.long}
            {!collateralRatio && <sup>2</sup>}
          </td>
          <td></td>
        </tr>
        <tr>
          <td className="sub">&nbsp;&nbsp;&nbsp;&nbsp;{tooltipText.spec}</td>
          <td>{`${numberParse(
            (collateralRatio
              ? breakdown.longFarmApr.spec(collateralRatio)
              : breakdown.longFarmApr.spec()) * 100
          )}%`}</td>
        </tr>
        <tr>
          <td className="sub">&nbsp;&nbsp;&nbsp;&nbsp;{tooltipText.mir}</td>
          <td>{`${numberParse(
            (collateralRatio
              ? breakdown.longFarmApr.mir(collateralRatio)
              : breakdown.longFarmApr.mir()) * 100
          )}%`}</td>
        </tr>
        <tr>
          <td className="sub">&nbsp;&nbsp;&nbsp;&nbsp;{tooltipText.trade}</td>
          <td>{`${numberParse(
            (collateralRatio
              ? breakdown.longFarmApr.trade(collateralRatio)
              : breakdown.longFarmApr.trade()) * 100
          )}%`}</td>
        </tr>
        <tr>
          <td>
            <span className="semiBold">
              {tooltipText.afterApr}
              {!collateralRatio && <sup>3</sup>}
            </span>
          </td>
          <td className="successColor">{`${numberParse(
            (collateralRatio
              ? breakdown.apr(collateralRatio)
              : breakdown.apr()) * 100
          )}%`}</td>
        </tr>
      </tbody>
    </table>
  );
};

export default AssetBreakdown;
