import React from 'react';
import * as AWS from 'aws-sdk';
import pool from '@ricokahler/pool';
import { useApp } from './AppContext';
import { useGas } from './GasContext';
import useUtils from '../hooks/useUtils';
import { useToken } from './TokenContext';
import { useQueries } from './QueriesContext';
import { useWallet } from '@terra-money/wallet-provider';
import { bombayAddress, columbiaAddress } from '../constants/tokenAddress';
import {
  terraManagerAddress,
  swapAddress,
} from '../constants/contractAddresses';
import {
  positionAddress,
  tokenBalanceQuery,
  positionByHolderQuery,
  positionDetails as fetchPositionDetails,
  positionInfo as positionInfoQuery,
  swapAddress as swapAddressQuery,
} from '../gql/hive/hiveGQL';

const DeltaContext = React.createContext();

export function useDelta() {
  return React.useContext(DeltaContext);
}

const DeltaProvider = ({ children }) => {
  const { coins } = useGas();
  const { exchange } = useToken();
  const { numberParse } = useUtils();
  const { status, network } = useWallet();
  const { holderAddress, balance } = useApp();
  const [coinSum, setCoinSum] = React.useState();
  const [deltaSum, setDeltaSum] = React.useState();
  const [holdings, setHoldings] = React.useState([]);
  const [balances, setBalances] = React.useState([]);
  const [posManager, setPosManager] = React.useState();
  const [balanceSum, setBalanceSum] = React.useState();
  const [initialSum, setInitialSum] = React.useState(0);
  const [positionInfo, setPositionInfo] = React.useState([]);
  const [positionValues, setPositionValues] = React.useState([]);
  const [positionInfoLookup, setPositionInfoLookup] = React.useState({});
  const [coinBalances, setCoinBalances] = React.useState([]);
  const [deltaPositions, setDeltaPositions] = React.useState([]);
  const [anchorInvestment, setAnchorInvestment] = React.useState([]);
  const { contractNetwork, lcd, addressProvider, userStatus, hiveClient } =
    useQueries();
  const [deltaIsLoading, setDeltaIsLoading] = React.useState(false);

  const getDeltaPositions = () => {
    const maxPositions = 100;
    setDeltaIsLoading(true);
    if (network && holderAddress && userStatus.connected) {
      const getPositions = () => {
        async function getInfo() {
          const query = positionInfoQuery(
            addressProvider.aTerra(),
            terraManagerAddress[contractNetwork],
            holderAddress,
            maxPositions,
            0
          );
          const hiveRes = await hiveClient.setQueries(query).request();
          const [aTerra, positions, position_manager] = hiveRes;
          return { positions, position_manager, aTerra };
        }
        getInfo().then((data) => {
          const allPositions = [];
          const getNextPositions = async (pos, max) => {
            if (pos.position_id_vec.length > 0) {
              let lastPosition =
                pos.position_id_vec[pos.position_id_vec.length - 1];

              const query = positionByHolderQuery(
                terraManagerAddress[contractNetwork],
                holderAddress,
                max,
                lastPosition
              );
              try {
                const [res] = await hiveClient.setQuery(query).request();
                allPositions.push(...pos.position_id_vec);
                getNextPositions(res, max);
              } catch (e) {
                console.error(e);
              }
            } else {
              allPositions.length === 0 &&
                allPositions.push(...pos.position_id_vec);
              return setDeltaPositions(allPositions);
            }
          };
          getNextPositions(data.positions, maxPositions);
          setAnchorInvestment({ value: data.aTerra.balance });
          setPosManager(data.position_manager.manager_addr);
        });
      };
      getPositions();
    }
    setDeltaIsLoading(false);
  };

  const getPositionDetails = (pos) => {
    if (posManager && userStatus.connected) {
      async function getPosDetails() {
        const query = positionAddress(posManager, pos, 3);
        try {
          const [res] = await hiveClient.setQuery(query).request();
          const positionQuery = fetchPositionDetails(res);
          const [positionDetails] = await hiveClient
            .setQuery(positionQuery)
            .request();
          return positionDetails;
        } catch (e) {
          console.error(e);
        }
      }
      return getPosDetails();
    }
  };

  const getPositionValues = (positions) => {
    setPositionValues([]);
    setInitialSum(0);
    if (!!positions) {
      let openPositions = positions.filter((pos) => !pos.closed);
      openPositions.forEach((pos) => {
        const documentClient = new AWS.DynamoDB.DocumentClient({
          region: 'us-west-2',
        });
        const query = async () => {
          const response = await documentClient
            .query({
              TableName:
                network.chainID === 'bombay-12'
                  ? 'position_ticks_dev'
                  : 'position_ticks',
              KeyConditionExpression: 'position_id = :position_id',
              ExpressionAttributeValues: {
                ':position_id': Number(pos.position),
              },
            })
            .promise();
          let initialValue = null;
          const items = response['Items'];
          if (items && items.length > 0) {
            initialValue = numberParse(items[0].uusd_value, 'small');
          } else if (pos.value) {
            initialValue = pos.value;
          }
          pos.initial = initialValue;
          const posValues = {
            items,
            position: pos.position,
          };
          setInitialSum((prevSum) => prevSum + initialValue);
          setPositionValues((positionValues) => [...positionValues, posValues]);
        };
        query().catch((error) => {
          console.error(JSON.stringify(error, null, 2));
        });
      });
    }
  };

  const refreshPositions = () => {
    setDeltaPositions([]);
    setPositionInfo([]);
    getDeltaPositions();
  };

  const clearBalances = () => {
    setBalances([]);
    setCoinBalances([]);
  };

  React.useEffect(() => {
    if (holderAddress) {
      const tokenAddress =
        network.chainID === 'bombay-12' ? bombayAddress : columbiaAddress;
      let token = [];
      let getBalance = async (add) => {
        const query = tokenBalanceQuery(add.address, holderAddress);
        try {
          const [res] = await hiveClient.setQuery(query).request();
          return {
            token: add.token,
            balance: res.balance,
            address: add.address,
          };
        } catch (e) {
          console.error(e);
        }
      };
      tokenAddress.forEach((add, i) => {
        getBalance(add).then((d) => {
          token.push(d);
          if (i === tokenAddress.length - 1) {
            setHoldings(token);
          }
        });
      });
    }
  }, [network, holderAddress]); // eslint-disable-line react-hooks/exhaustive-deps

  React.useEffect(() => {
    if (balance && holdings.length !== 0 && balances.length === 0 && exchange) {
      let convertAUST = async (add) => {
        const query = tokenBalanceQuery(
          addressProvider.aTerra(),
          holderAddress
        );
        try {
          const [res] = await hiveClient.setQuery(query).request();
          return {
            token: add.token,
            amount: (
              Number(res.balance) * Number(exchange.exchange_rate)
            ).toFixed(0),
            address: add.address,
          };
        } catch (e) {
          console.error(e);
        }
      };
      let convertBalance = async (add) => {
        const query = swapAddressQuery(
          swapAddress[contractNetwork],
          add.balance,
          'terra',
          add.address,
          'uusd'
        );
        try {
          const [res] = await hiveClient.setQuery(query).request();
          return {
            token: add.token,
            amount: res.amount,
            address: add.address,
          };
        } catch (e) {
          console.error(e);
        }
      };
      setBalances((balances) => [
        ...balances,
        { token: 'ust', amount: numberParse(balance, 'small') },
      ]);
      holdings.forEach((add, i) => {
        add.token === 'aust'
          ? convertAUST(add).then((d) => {
              setBalances((balances) => [...balances, d]);
            })
          : convertBalance(add).then((d) => {
              setBalances((balances) => [...balances, d]);
            });
      });
    }
  }, [holdings, balance, exchange]); // eslint-disable-line react-hooks/exhaustive-deps

  React.useEffect(() => {
    if (coinBalances.length === 0 && coins) {
      let convertCoins = async (c) => {
        const query = swapAddressQuery(
          swapAddress[contractNetwork],
          c.amount,
          'native',
          c.denom,
          'uusd'
        );
        try {
          const [res] = await hiveClient.setQuery(query).request();
          return { token: c.denom, amount: res.amount };
        } catch (e) {
          console.error(e);
        }
      };

      let coinBalance = [];
      let newCoins = coins.filter((f) => f.denom === 'uluna');
      newCoins.forEach((c, i) => {
        convertCoins(c).then((d) => {
          coinBalance.push(d);
          setCoinBalances(coinBalance);
        });
      });
    }
  }, [coins, refreshPositions]); // eslint-disable-line react-hooks/exhaustive-deps

  React.useEffect(() => {
    if (network && holderAddress && status && !!lcd) {
      getDeltaPositions();
    }
  }, [network, holderAddress, status, lcd]); // eslint-disable-line react-hooks/exhaustive-deps

  React.useEffect(() => {
    if (balances.length === 5 && coinBalances.length <= 2) {
      let sumArr = (arr) => {
        let sum = 0;
        for (let i = 0; i < arr.length; i++) {
          sum += Number(arr[i].amount);
        }
        return numberParse(sum, 'small');
      };
      setBalanceSum(sumArr(balances));
      setCoinSum(sumArr(coinBalances));
    }
  }, [balances, coinBalances]); // eslint-disable-line react-hooks/exhaustive-deps

  React.useEffect(() => {
    if (deltaPositions.length !== 0) {
      let positionArr = [];
      let lookup = {};
      let count = 0;
      let sum = 0;
      let getAddress = async (add) => {
        let name = await lcd.wasm.contractInfo(add);
        return name;
      };

      (async function deltaPositionsPool() {
        const setDeltaPositions = await pool({
          collection: deltaPositions,
          maxConcurrency: 5,
          task: async (pos) => {
            let positionData = await getPositionDetails(pos);
            return { pos, positionData };
          },
        });

        return setDeltaPositions;
      })().then((res) => {
        res.forEach((data, i) => {
          const { pos, positionData } = data;
          let obj = {};
          getAddress(positionData.mirror_asset_cw20_addr).then((res) => {
            obj.name = res.init_msg.symbol;
          });
          obj.value = positionData.detailed_info
            ? numberParse(positionData.detailed_info.uusd_value, 'small')
            : numberParse(
                positionData.position_close_info.uusd_amount,
                'small'
              );
          obj.initial = numberParse(
            positionData.position_open_info.uusd_amount,
            'small'
          );
          obj.time = positionData.position_open_info.time_nanoseconds;
          obj.address = positionData.mirror_asset_cw20_addr;
          obj.position = pos;
          obj.closed = positionData.position_close_info ? true : false;
          sum = positionData.position_close_info
            ? sum
            : sum + Number(obj.value);
          setDeltaSum(sum);
          positionArr.push(obj);
          lookup[pos] = count++;
          if (i + 1 === deltaPositions.length) {
            getPositionValues(positionArr);
            setPositionInfo(positionArr);
            setPositionInfoLookup(lookup);
          }
        });
      });

      deltaPositions.forEach((f, i) => {});
    }
  }, [posManager, network, deltaPositions]); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <DeltaContext.Provider
      value={{
        coinSum,
        deltaSum,
        balances,
        initialSum,
        balanceSum,
        posManager,
        coinBalances,
        positionInfo,
        positionValues,
        clearBalances,
        refreshPositions,
        anchorInvestment,
        positionInfoLookup,
        deltaIsLoading,
      }}
    >
      {children}
    </DeltaContext.Provider>
  );
};

export { DeltaProvider, DeltaContext };
