import React from 'react';
import textContext from '../../textContext';
import { useDelta } from '../../context/DeltaContext';
import { useApp } from '../../context/AppContext';
import { useWallet } from '@terra-money/wallet-provider';
import useUtils from '../../hooks/useUtils';

// Comps
import ApertureInvestments from './ApertureInvestments';
import TokenHoldings from './TokenHoldings';

export default function UserAccount(props) {
  const { numberParse } = useUtils();
  const { language, balance } = useApp();
  const { deltaSum, balanceSum, coinSum } = useDelta();
  const { status } = useWallet();
  const text = textContext[language].portfolio.dashboard;
  const totalBalance = balanceSum + coinSum;

  return (
    <div id="auth" className="wrapper authClass">
      <div className="top">
        <h3>{text.total}</h3>
      </div>
      <hr />
      <div className="body">
        <p>{text.aperture}</p>
        <h3>{deltaSum ? numberParse(deltaSum, 'currency') : '- -'}</h3>
        <ApertureInvestments handleSelect={props.handleSelect} />
      </div>
      <hr />
      <div className="body">
        <p>{text.holdings}</p>
        <h3>
          {status === 'WALLET_CONNECTED' && balance && !isNaN(totalBalance)
            ? numberParse(totalBalance, 'currency')
            : '- -'}
        </h3>
        <TokenHoldings />
      </div>
    </div>
  );
}
