import React from 'react';
import textContext from '../../textContext';
import { useApp } from '../../context/AppContext';
import { useWallet } from '@terra-money/wallet-provider';
import { useDelta } from '../../context/DeltaContext';
import useUtils from '../../hooks/useUtils';

export default function Total(props) {
  const { language } = useApp();
  const { status } = useWallet();
  const { numberParse } = useUtils();
  const { balanceSum, coinSum } = useDelta();
  const text = textContext[language].portfolio;

  const coinCheck = isNaN(coinSum) ? 0 : coinSum;
  const balanceCheck = isNaN(balanceSum) ? 0 : balanceSum;
  const deltaCheck = isNaN(props.deltaTotal) ? 0 : props.deltaTotal;
  const totalBalance = balanceCheck + coinCheck;

  return (
    <div onClick={() => props.handleSelect({ title: text.total })} id="total">
      <div
        className={`wrapper ${
          status !== 'WALLET_CONNECTED'
            ? 'selected'
            : !props.position && 'selected'
        }`}
      >
        <span className="totalClass">
          <p className="title">{text.total}</p>
          {status === 'WALLET_CONNECTED' ? (
            <h2 style={{ marginBottom: '.5rem' }}>
              {props.deltaTotal
                ? numberParse(deltaCheck + totalBalance, 'currency')
                : numberParse(totalBalance, 'currency')}
            </h2>
          ) : (
            <h2>- -</h2>
          )}
        </span>
      </div>
    </div>
  );
}
