import React from 'react';
import { useDelta } from '../../context/DeltaContext';

// Comps
import Assets from './Assets';
import Value from './Value';
import Charts from './Charts';

export default function SelectedInvest(props) {
  const { positionInfo, positionValues, anchorInvestment, deltaIsLoading } =
    useDelta();
  const positionData = positionValues.find(
    (pos) => props.position === pos.position
  );

  return (
    <div className="wrapper">
      {props.position === 'anc' ? (
        <React.Fragment>
          <Assets
            position={props.position}
            name={props.name}
            anchor={props.position}
            time={props.time}
            address={props.address}
            value={anchorInvestment.value}
          />
          <hr />
          <Value
            data={positionData}
            anchor={props.position}
            position={props.position}
            time={props.time}
            value={anchorInvestment.value}
            initial={props.initial}
            loading={deltaIsLoading}
          />
          <hr />
          <Charts data={positionData} position={props.position} />
        </React.Fragment>
      ) : (
        positionInfo
          .filter((f) => f.position === props.position)
          .map((pos, i) => (
            <React.Fragment key={i}>
              <Assets
                position={pos.position}
                name={pos.name}
                anchor={props.position.value}
                time={pos.time}
                address={pos.address}
                value={pos.value}
              />
              <hr />
              <Value
                data={positionData}
                anchor={props.position.value}
                position={pos.position}
                time={pos.time}
                value={pos.value}
                initial={pos.initial}
                loading={deltaIsLoading}
              />
              <hr />
              <Charts
                info={positionInfo}
                data={positionData}
                position={pos.position}
              />
            </React.Fragment>
          ))
      )}
    </div>
  );
}
