import * as React from 'react';
import axios from 'axios';
import { useApp } from './AppContext';
import useUtils from '../hooks/useUtils';
import { useQueries } from './QueriesContext';
import { useWallet } from '@terra-money/wallet-provider';
import { Coins, Coin, MsgExecuteContract } from '@terra-money/terra.js';
import {
  encodingAddress,
  gasNetworkRequest,
  terraManagerAddress,
} from '../constants/contractAddresses';

const GasContext = React.createContext();

export function useGas() {
  return React.useContext(GasContext);
}

const GasProvider = ({ children }) => {
  const { network } = useWallet();
  const { numberParse } = useUtils();
  const [coins, setCoins] = React.useState();
  const { holderAddress, balance } = useApp();
  const [gasFees, setGasFees] = React.useState();
  const [ancInvFee, setAncInv] = React.useState({ amount: 0 });
  const [deltaInvFee, setDeltaInv] = React.useState({ amount: 0 });
  const { contractNetwork, addressProvider, userStatus, lcd } = useQueries();
  const [selectGas, setSelectGas] = React.useState({
    name: 'uusd',
    price: '0.2',
  });

  const setUserCoins = () => {
    const coins = async () => {
      let bal = await lcd.bank.balance(holderAddress);
      return bal;
    };
    coins().then((bal) => {
      let newCoin = new Coins(bal[0]).toData();
      setCoins(newCoin);
    });
  };

  async function getGasFee(msgs) {
    const accountInfo = await lcd.auth.accountInfo(holderAddress);
    let denoms = selectGas.name;
    let fees = selectGas.price;
    const feeDenoms = [denoms];
    const gasPrices = {
      [denoms]: fees,
    };
    const txOptions: CreateTxOptions = { msgs, gasPrices, feeDenoms };
    // Test raw estimate fee function with specified gas
    let rawFee = undefined;
    try {
      rawFee = await lcd.tx.estimateFee(
        [
          {
            sequenceNumber: accountInfo.getSequenceNumber(),
            publicKey: accountInfo.getPublicKey(),
          },
        ],
        txOptions
      );
    } catch (err) {
      // console.log(err);
      // throw new Error(err.message);
    }
    return rawFee;
  }

  const getFees = () => {
    if (gasFees && holderAddress && balance) {
      let anchorMsgs = [
        new MsgExecuteContract(
          holderAddress,
          addressProvider.market(),
          { deposit_stable: {} },
          [new Coin('uusd', numberParse(balance, 'invest'))]
        ),
      ];
      let encAddress = encodingAddress[contractNetwork];
      let encode_data = {
        target_min_collateral_ratio: '2.5',
        target_max_collateral_ratio: '2.9',
        mirror_asset_cw20_addr: encAddress,
      };
      let encoding = btoa(JSON.stringify(encode_data));
      const deltaMsg = [
        new MsgExecuteContract(
          holderAddress,
          terraManagerAddress[contractNetwork],
          {
            create_position: {
              data: encoding,
              assets: [
                {
                  info: { native_token: { denom: 'uusd' } },
                  amount: numberParse(balance, 'invest'),
                },
              ],
              strategy: { chain_id: 3, strategy_id: '0' },
            },
          },
          [new Coin('uusd', numberParse(balance, 'invest'))]
        ),
      ];

      if (balance !== 0) {
        getGasFee(deltaMsg).then((d) => {
          setDeltaInv(d.toData().amount[0]);
        });
        getGasFee(anchorMsgs).then((d) => {
          setAncInv(d.toData().amount[0]);
        });
      }
    }
  };

  React.useEffect(() => {
    if (holderAddress && network && userStatus.connected) {
      setUserCoins();
    }
  }, [holderAddress, network]); // eslint-disable-line react-hooks/exhaustive-deps

  React.useEffect(() => {
    if (network && lcd && userStatus.connected) {
      let networkReq = gasNetworkRequest[contractNetwork];
      axios.get(networkReq).then((res) => {
        const resFees = Object.entries(res.data).filter((f) => {
          return (
            [
              'uusd',
              'uluna',
              'ukrw',
              'usdr',
              'umnt',
              'ueur',
              'ucny',
              'ujpy',
              'ugbp',
              'uinr',
              'ucad',
              'uchf',
              'uaud',
              'usgd',
              'uthb',
              'uhkd',
              'udkk',
              'uidr',
              'umyr',
              'unok',
              'uphp',
              'usek',
              'utwd',
            ].indexOf(f[0]) !== -1
          );
        });
        let filteredFees = resFees.map((d) => {
          return { name: d[0], price: d[1] };
        });
        setGasFees(filteredFees);
      });
    }
  }, [network, lcd]); // eslint-disable-line react-hooks/exhaustive-deps

  React.useEffect(() => {
    getFees();
  }, [gasFees, selectGas, balance, holderAddress]); // eslint-disable-line react-hooks/exhaustive-deps

  // Token gas price switch
  const gasFeeName = (name) => {
    switch (name) {
      case 'uusd':
        return 'UST';
      case 'uluna':
        return 'Luna';
      case 'ukrw':
        return 'KRT';
      case 'usdr':
        return 'SDT';
      case 'umnt':
        return 'MNT';
      case 'ueur':
        return 'EUT';
      case 'ucny':
        return 'CNT';
      case 'ujpy':
        return 'JPT';
      case 'ugbp':
        return 'GBT';
      case 'uinr':
        return 'INT';
      case 'ucad':
        return 'CAT';
      case 'uchf':
        return 'CHT';
      case 'uaud':
        return 'AUT';
      case 'usgd':
        return 'SGT';
      case 'uthb':
        return 'THT';
      case 'uhkd':
        return 'HKT';
      case 'udkk':
        return 'DKT';
      case 'uidr':
        return 'IDT';
      case 'umyr':
        return 'MYT';
      case 'unok':
        return 'NOT';
      case 'uphp':
        return 'PHT';
      case 'usek':
        return 'SET';
      case 'utwd':
        return 'TWT';
      default:
        return;
    }
  };

  return (
    <GasContext.Provider
      value={{
        coins,
        getFees,
        gasFees,
        ancInvFee,
        selectGas,
        gasFeeName,
        deltaInvFee,
        setUserCoins,
        setSelectGas,
        getGasFee,
      }}
    >
      {children}
    </GasContext.Provider>
  );
};

export { GasProvider, GasContext };
