import * as React from 'react';
import { useWallet } from '@terra-money/wallet-provider';
import { mapplContract } from '../constants/contractAddresses';
import axios from 'axios';

const AppContext = React.createContext();

export function useApp() {
  return React.useContext(AppContext);
}

const AppProvider = ({ children }) => {
  const { wallets, status, network } = useWallet();
  const [error, setError] = React.useState();
  const [mirrorError, setMirrorError] = React.useState();
  const [errMsg, setErrMsg] = React.useState();
  const [result, setResult] = React.useState();
  const [holderAddress, setholderAddr] = React.useState();
  const [balance, setBalance] = React.useState();
  const [eTHBalance, setETHBalance] = React.useState();
  const [success, setSuccess] = React.useState();
  const [loading, setLoading] = React.useState();
  const [progress, setProgress] = React.useState([]);
  const [language, setLanguage] = React.useState('english');
  const [width, setwidth] = React.useState(window.innerWidth);
  const [backendData, setBackendData] = React.useState();
  const [isMarketHour, setIsMarketHour] = React.useState(false);

  /* Dirty flag to trigger backendData refresh.
  Only set dirty during major page changes for now. */
  const [backendDataIsDirty, setBackendDataIsDirty] = React.useState(true);

  React.useEffect(() => {
    // Check local storage on page load to get last saved language
    const langCheck = localStorage.getItem('language');
    if (langCheck !== null) {
      setLanguage(langCheck);
    }
    // Set primary language to english
    else {
      setLanguage('english');
    }
  }, []);

  // Set Width
  React.useEffect(() => {
    window.addEventListener('resize', (x) => setwidth(x.target.innerWidth));
  }, [width]);

  const isMarketHourCondition = (timestamp_sec) => {
    // Treat data updated within last 2 minutes as within market hour.
    return new Date() - new Date(parseInt(timestamp_sec) * 1e3) <= 2 * 60 * 1e3;
  };

  React.useEffect(() => {
    const marketHour = (data) => {
      console.log("network.chainID: ", network.chainID);
      const mainnetAAPL = mapplContract.columbus;
      const testnetAAPL = mapplContract.bombay;
      const defaultAssetAddr =
        network.chainID === 'columbus-5' ? mainnetAAPL : testnetAAPL;
      // Default to AAPL to test market hour condition.
      const oracleQueries = data.oracle_queries;
      console.log("oracle queries: ", oracleQueries);
      console.log("defaultAssetAddr: ", defaultAssetAddr);
      const lastUpdatedBase = oracleQueries[defaultAssetAddr].last_updated_base;
      setIsMarketHour(isMarketHourCondition(lastUpdatedBase));
    };
    const getBackendData = async () => {
      const res = await axios.get(
        'https://nh4146kf45.execute-api.us-west-2.amazonaws.com/prod/frontend-data',
        {
          params: {
            network: network.chainID === 'bombay-12' ? 'testnet' : 'mainnet',
          },
        }
      );
      if (res.data) {
        setBackendData(res.data);
        marketHour(res.data);
        if (status === 'WALLET_CONNECTED') setBackendDataIsDirty(false);
      }
    };
    if (backendDataIsDirty && !!network && !!network.chainID) {
      getBackendData();
    }
  }, [backendDataIsDirty, network, status]);

  // Langauge update function
  const langSelect = (lan) => {
    // Update state of language in application and local storage
    setLanguage(lan);
    localStorage.setItem('language', lan);
  };

  // Check if has access
  const setAccess = () => {
    localStorage.setItem('hasAccess', true);
  };
  const getAccess = localStorage.getItem('hasAccess');

  // Check if accepted
  const setAccept = () => {
    // Update state of language in application and local storage
    localStorage.setItem('checkDisclaimer', true);
  };
  const getAccept = localStorage.getItem('checkDisclaimer');

  const getToken = (t) => {
    t && t !== '0' ? setholderAddr(t) : setholderAddr('0');
  };
  const deleteToken = () => {
    setBalance();
    setholderAddr();
  };
  React.useEffect(() => {
    if (status === 'WALLET_CONNECTED') {
      getToken(wallets[0].terraAddress);
    }
  }, [status, wallets]);

  React.useEffect(() => {
    if (result) {
      const tx = result.result.txhash;
      setProgress((p) => [...p, tx]);
      setLoading(false);
      setSuccess(true);
    }
  }, [result]);

  React.useEffect(() => {
    if (error) {
      setErrMsg(error);
      setTimeout(() => {
        setErrMsg();
      }, 10000);
    }
  }, [error]);

  return (
    <AppContext.Provider
      value={{
        width,
        errMsg,
        loading,
        success,
        holderAddress,
        balance,
        getToken,
        language,
        setError,
        progress,
        setholderAddr,
        setAccept,
        getAccept,
        setErrMsg,
        setResult,
        setLoading,
        setSuccess,
        eTHBalance,
        setBalance,
        langSelect,
        setProgress,
        deleteToken,
        mirrorError,
        setETHBalance,
        setMirrorError,
        isMarketHourCondition,
        isMarketHour,
        backendData,
        setBackendDataIsDirty,
        setAccess,
        getAccess,
      }}
    >
      {children}
    </AppContext.Provider>
  );
};

export { AppProvider, AppContext };
