import { useApp } from '../context/AppContext';
import { useQueries } from '../context/QueriesContext';
import { useGas } from '../context/GasContext';
import axios from 'axios';
import {
  CreateTxFailed,
  Timeout,
  TxFailed,
  TxUnspecifiedError,
  useConnectedWallet,
  UserDenied,
} from '@terra-money/wallet-provider';
import {
  MsgExecuteContract,
  TxBody,
  AuthInfo,
  Fee,
  Coins,
  Coin,
  Tx,
} from '@terra-money/terra.js';
import { terraManagerAddress } from '../constants/contractAddresses';

const useInvest = () => {
  const { selectGas } = useGas();
  const connectedWallet = useConnectedWallet();
  const { setError, setLoading, setResult, holderAddress, setMirrorError } =
    useApp();
  const { lcd, addressProvider, contractNetwork } = useQueries();

  let getGasFee = async (msgs) => {
    let denoms = selectGas.name;
    let fees = selectGas.price;
    const accountInfo = await lcd.auth.accountInfo(holderAddress);
    const feeDenoms = [denoms];
    const gasPrices = { [denoms]: fees };
    const txOptions: CreateTxOptions = {
      msgs,
      gasPrices,
      feeDenoms,
    };
    // Test raw estimate fee function with specified gas
    let rawFee = undefined;
    try {
      rawFee = await lcd.tx.estimateFee(
        [
          {
            sequenceNumber: accountInfo.getSequenceNumber(),
            publicKey: accountInfo.getPublicKey(),
          },
        ],
        txOptions
      );
    } catch (err) {
      console.log(err.message);
    }
    return rawFee;
  };

  const anchorInvestMsgs = (amount) => {
    const msg = [
      new MsgExecuteContract(
        holderAddress,
        addressProvider.market(),
        {
          deposit_stable: {},
        },
        [new Coin('uusd', (amount * 1e6).toFixed(0))]
      ),
    ];
    return msg;
  };

  const anchorWithdrawMsg = (amount, address, network) => {
    const msg = [
      new MsgExecuteContract(holderAddress, address, {
        send: {
          amount: (amount * 1e6).toFixed(0),
          contract: network,
          msg: 'eyJyZWRlZW1fc3RhYmxlIjp7fX0=',
        },
      }),
    ];
    return msg;
  };

  const deltaInvestMsg = (encoding, amount) => {
    const msg = [
      new MsgExecuteContract(
        holderAddress,
        terraManagerAddress[contractNetwork],
        {
          create_position: {
            data: encoding,
            assets: [
              {
                info: {
                  native_token: {
                    denom: 'uusd',
                  },
                },
                amount: (amount * 1e6).toString(),
              },
            ],
            strategy: {
              chain_id: 3,
              strategy_id: '0',
            },
          },
        },
        [new Coin('uusd', (amount * 1e6).toString())]
      ),
    ];
    return msg;
  };

  const deltaWithdrawMsg = (position, posManager) => {
    const msg = [
      new MsgExecuteContract(
        holderAddress,
        posManager,
        {
          migrate_position_contracts: {
            positions: [
              {
                chain_id: 3,
                position_id: position && position.toString(),
              },
            ],
            position_contracts: [],
          },
        },
        []
      ),
      new MsgExecuteContract(
        holderAddress,
        terraManagerAddress[contractNetwork],
        {
          execute_strategy: {
            action: {
              close_position: {
                recipient: {
                  terra_chain: {
                    recipient: holderAddress,
                  },
                },
              },
            },
            assets: [],
            position_id: position && position.toString(),
          },
        },
        []
      ),
    ];
    return msg;
  };

  const deltaWithdrawSimulateMsg = (position) => {
    const msg = [
      new MsgExecuteContract(
        holderAddress,
        terraManagerAddress[contractNetwork],
        {
          execute_strategy: {
            action: {
              close_position: {
                recipient: {
                  terra_chain: {
                    recipient: holderAddress,
                  },
                },
              },
            },
            assets: [],
            position_id: position && position.toString(),
          },
        },
        []
      ),
    ];
    return msg;
  };

  const executeInvest = (msgs, gasFee, type) => {
    if (!connectedWallet) {
      return;
    }
    setResult(null);
    setError(null);
    console.log("[before post] network: ", connectedWallet.network);
    connectedWallet
      .post({
        fee: gasFee,
        msgs: msgs,
      })
      .then((res) => {
        setResult(res);
      })
      .catch((err) => {
        setLoading(false);
        if (err instanceof UserDenied) {
          setError(
            'Error: User has cancelled the transaction, please try again.'
          );
          setMirrorError(err);
        } else if (err instanceof CreateTxFailed) {
          setError('Error: Create Tx failed, please try again.');
          setMirrorError(err);
        } else if (err instanceof TxFailed) {
          setError('Error: Tx failed, please try again.' + String(err.message));
          setMirrorError(err);
        } else if (err instanceof Timeout) {
          setError('Error: Network has timed out, please try again.');
          setMirrorError(err);
        } else if (err instanceof TxUnspecifiedError) {
          setError('Error: Unspecified error, please try again.');
          setMirrorError(err);
        } else {
          setError(
            'Unknown Error: ' +
              (err instanceof Error ? err.message : String(err))
          );
        }
      });
  };
  const simulateInvest = async (msgs, type) => {
    const accountInfo = await lcd.auth.accountInfo(holderAddress);
    const txBody = new TxBody(msgs, '');
    const authInfo = new AuthInfo([], new Fee(0, new Coins()));
    const tx = new Tx(txBody, authInfo, []);
    tx.appendEmptySignatures([
      {
        sequenceNumber: accountInfo.getSequenceNumber(),
        publicKey: accountInfo.getPublicKey(),
      },
    ]);
    try {
      const res = await axios.post(
        `${lcd.config.URL}/cosmos/tx/v1beta1/simulate`,
        { tx_bytes: Buffer.from(tx.toBytes(true)).toString('base64') }
      );
      return res;
    } catch (err) {
      return err;
    }
  };
  return {
    getGasFee,
    executeInvest,
    deltaInvestMsg,
    deltaWithdrawSimulateMsg,
    anchorInvestMsgs,
    deltaWithdrawMsg,
    anchorWithdrawMsg,
    simulateInvest,
  };
};

export default useInvest;
