import React from 'react';
import useInvest from '../../hooks/useInvest';
import { useApp } from '../../context/AppContext';
import { useToken } from '../../context/TokenContext';
import { useDelta } from '../../context/DeltaContext';

// Comps
import Spinner from '../layout/Spinner';
import Success from '../layout/Success';

export default function InvestButton(props) {
  const {
    plus,
    getGas,
    invest,
    amount,
    network,
    address,
    checkGas,
    position,
    collateral,
    deltaFeeInit,
    anchorFeeInit,
    withdrawValue,
  } = props;
  const { balance, loading, success, setErrMsg, setLoading } = useApp();
  const {
    getGasFee,
    executeInvest,
    deltaInvestMsg,
    anchorInvestMsgs,
    deltaWithdrawMsg,
    anchorWithdrawMsg,
  } = useInvest();
  const { exchange } = useToken();
  const { posManager } = useDelta();

  const handleInvest = () => {
    setLoading(true);
    let type = plus ? 'delta' : 'anchor';

    let encoding;
    if (plus) {
      let encode_data = plus && {
        target_min_collateral_ratio: (collateral / 100 - 0.2)
          .toFixed(2)
          .toString(),
        target_max_collateral_ratio: (collateral / 100 + 0.2)
          .toFixed(2)
          .toString(),
        mirror_asset_cw20_addr: address,
      };
      encoding = btoa(JSON.stringify(encode_data));
    }
    let ancWithdrawAmount =
      withdrawValue && Number(amount) / Number(exchange.exchange_rate);

    if (!plus) {
      let anchorTypeMsg = invest
        ? anchorInvestMsgs(amount)
        : anchorWithdrawMsg(ancWithdrawAmount, address, network);
      getGasFee(anchorTypeMsg)
        .then((data) => {
          executeInvest(anchorTypeMsg, data, type);
        })
        .catch((err) => {
          console.log(err);
          setLoading(false);
          err.response.status === 400
            ? setErrMsg('Error: Failed to execute message, please try again.')
            : setErrMsg('Error: Unspecified error, please try again.');
        });
    } else {
      const memo = invest
        ? 'Open delta-neutral position'
        : 'Close delta-neutral position';
      const deltaMsgs = invest
        ? deltaInvestMsg(encoding, amount)
        : deltaWithdrawMsg(position, posManager);
      getGasFee(deltaMsgs)
        .then((data) => {
          executeInvest(deltaMsgs, data, type, memo);
        })
        .catch((err) => {
          setLoading(false);
          err.response.status === 400
            ? setErrMsg('Error: Failed to execute message, please try again.')
            : setErrMsg('Error: Unspecified error, please try again.');
        });
    }
  };

  return (
    <div className="confirmAsset">
      <button
        onClick={handleInvest}
        disabled={
          plus && invest
            ? true
            : loading
            ? true
            : checkGas === -1
            ? true
            : getGas &&
              (plus ? deltaFeeInit : anchorFeeInit) > getGas.amount / 1e6
            ? true
            : getGas &&
              (plus ? deltaFeeInit : anchorFeeInit) > getGas.amount / 1e6
            ? 'disabled'
            : plus && !invest
            ? false
            : plus &&
              amount >= 2000 &&
              amount <= (invest ? balance : withdrawValue)
            ? false
            : !plus &&
              amount > (invest ? 0.000001 : 0) &&
              amount <= (invest ? balance : withdrawValue)
            ? false
            : true
        }
        className={
          plus && invest
            ? 'disabled'
            : checkGas === -1
            ? 'disabled'
            : getGas &&
              (plus ? deltaFeeInit : anchorFeeInit) > getGas.amount / 1e6
            ? 'disabled'
            : getGas &&
              (plus ? deltaFeeInit : anchorFeeInit) > getGas.amount / 1e6
            ? 'disabled'
            : plus && !invest
            ? ''
            : plus &&
              amount >= 2000 &&
              amount <= (invest ? balance : withdrawValue)
            ? ''
            : !plus &&
              amount > (invest ? 0.000001 : 0) &&
              amount <= (invest ? balance : withdrawValue)
            ? ''
            : 'disabled'
        }
      >
        {success ? (
          <Success />
        ) : loading ? (
          <Spinner />
        ) : invest ? (
          'Deposit'
        ) : (
          'Withdraw'
        )}
      </button>
    </div>
  );
}
