// TODO: Export to NPM package.
import { request } from 'graphql-request';
import JSON5 from 'json5';

const TERRA_HIVE_ENDPOINT = {
  MAINNET: 'https://hive.terra.dev/graphql',
  TESTNET: 'https://testnet-hive.terra.dev/graphql',
};

class HiveRequestError extends Error {
  constructor(message: string) {
    super(message);
    this.name = 'HiveRequestError';
  }
}

class TerraHiveClientError extends Error {
  constructor(message: string) {
    super(message);
    this.name = 'TerraHiveClientError';
  }
}

interface TerraHiveQuery {
  contractAddress: string;
  query: object;
}

export enum TerraNetwork {
  MAINNET = 'columbus-5',
  TESTNET = 'bombay-12',
}

export default class TerraHiveClient {
  queries: Array<object> = [];
  network: string;

  constructor(network: string) {
    // if (!Object.values(TerraNetwork as any).includes(network))
    //   throw new TerraHiveClientError('Network not supported.');
    this.network = "columbus-5";
  }

  _genKeys = (amount: number) => {
    let arr = new Array(amount);
    for (let i = 0; i < amount; i++) {
      arr[i] = `q${i}`;
    }
    return arr;
  };

  _wasmQuery(tag: string, addr: string, query: object) {
    return `
      ${tag}: wasm {
          contractQuery(
            contractAddress: "${addr}"
            query: ${JSON5.stringify(query, { quote: '"' })}
          )
      }`;
  }
  _createHiveQuery = async () => {
    if (!this.queries || this.queries.length < 1) {
      throw new HiveRequestError('Queries cannont be empty.');
    }
    const keys = this._genKeys(this.queries.length);
    const queries = this.queries.map((e, i) => {
      const q = e as TerraHiveQuery;
      return this._wasmQuery(keys[i], q.contractAddress, q.query);
    });
    try {
      const res = await request(
        this.network === TerraNetwork.TESTNET
          ? TERRA_HIVE_ENDPOINT.TESTNET
          : TERRA_HIVE_ENDPOINT.MAINNET,
        '{' + queries.join(',') + '}'
      );
      return { keys, res };
    } catch (e: any) {
      throw new HiveRequestError(e.message);
    }
  };

  setQueries(queries: Array<object>) {
    this.queries = queries;
    return this;
  }

  setQuery(query: object) {
    this.queries = [query];
    return this;
  }

  addQuery(query: object) {
    this.queries.push(query);
    return this;
  }

  addQueries(queries: object) {
    this.queries.concat(queries);
    return this;
  }

  getQueries() {
    return this.queries;
  }

  /**
   * Send GQL request of current queries. Resets queries after request.
   * @returns Array of query results objects in order.
   */
  request = async (): Promise<object[] | null> => {
    let data = null;
    let resultArray = null;
    try {
      data = await this._createHiveQuery();
      const { keys, res } = data;
      resultArray = keys.map((x) => res[x].contractQuery);
    } catch (e: any) {
      console.error(e);
    } finally {
      this.queries = [];
    }
    return resultArray!;
  };
}
