import React from 'react';
// Terra
import { LCDClient } from '@terra-money/terra.js';
import { useWallet } from '@terra-money/wallet-provider';
// Anchor
import { AnchorEarn, Account, CHAINS } from '@anchor-protocol/anchor-earn';
import {
  columbus5,
  bombay12,
  AddressProviderFromJson,
  AddressMap,
} from '@anchor-protocol/anchor.js';
import TerraHiveClient from '../lib/TerraHiveClient';

interface QueriesContextInterface {
  lcd: LCDClient | null;
  account: Account;
  userStatus: Object;
  anchorEarn: AnchorEarn;
  anchorNetwork: Object;
  currentNetwork: AddressMap | null;
  addressProvider: AddressProviderFromJson;
  contractNetwork: String | null;
  hiveClient: TerraHiveClient | null;
}

type Props = {
  children: JSX.Element;
};

const QueriesContext = React.createContext<QueriesContextInterface | null>(
  null
);

export function useQueries() {
  return React.useContext(QueriesContext);
}
const QueriesProvider: React.FC<Props> = ({ children }) => {
  const [currentNetwork, setCurrentNetwork] = React.useState<AddressMap | null>(
    null
  );
  const [contractNetwork, setContractNetwork] = React.useState<String | null>(
    null
  );
  const [lcd, setLcd] = React.useState<LCDClient | null>(null);

  const { network, status } = useWallet();

  const [hiveClient, setHiveClient] = React.useState<TerraHiveClient | null>(
    null
  );

  React.useEffect(() => {
    const getCurrentNetwork = columbus5;
    setCurrentNetwork(getCurrentNetwork);
    const getContractNetwork = 'columbus';
    setContractNetwork(getContractNetwork);
    const getNetworkUrl = process.env.REACT_APP_MAINNET_NODE;
    console.log("LCD chain id: ", network.chainID);
    console.log("getNetworkUrl: ", getNetworkUrl);
    
    
    setLcd(new LCDClient({ URL: getNetworkUrl!, chainID: "columbus-5" }));
    setHiveClient(new TerraHiveClient(network.chainID));
  }, [network.chainID]);

  const addressProvider = new AddressProviderFromJson(currentNetwork!);

  const account = new Account(CHAINS.TERRA);

  const userStatus = {
    initializing: status === 'INITIALIZING' ? true : false,
    connected: status === 'WALLET_CONNECTED' ? true : false,
    not_connected: status === 'WALLET_NOT_CONNECTED' ? true : false,
  };

  const anchorNetwork =
    network.chainID === 'bombay-12'
      ? { id: 1, network: bombay12 }
      : { id: 0, network: columbus5 };

  const anchorEarn = new AnchorEarn({
    chain: CHAINS.TERRA,
    network: anchorNetwork.id,
    address: account.accAddress,
  });

  return (
    <QueriesContext.Provider
      value={{
        lcd,
        account,
        userStatus,
        anchorEarn,
        anchorNetwork,
        currentNetwork,
        addressProvider,
        contractNetwork,
        hiveClient,
      }}
    >
      {children}
    </QueriesContext.Provider>
  );
};

export { QueriesProvider, QueriesContext };
