import { useState } from 'react';
import textContext from '../../textContext';
import { useApp } from '../../context/AppContext';
import { useToken } from '../../context/TokenContext';

// Comps
import Search from './Search';
import Invest from './Invest';

export default function InvestPanel() {
  const { language } = useApp();
  const text = textContext[language].investment.invest;
  const [search, setSearch] = useState('');
  const { investData, investPlus } = useToken();
  const handleSearch = (fil) => {
    setSearch(fil);
  };

  return (
    <div>
      <Search handleSearch={handleSearch} />
      <Invest
        plus
        data={investPlus}
        search={search}
        filters={text.filterPlus}
        title={text.title.investPlus}
      />
      <Invest
        data={investData}
        search={search}
        filters={text.filter}
        title={text.title.invest}
      />
    </div>
  );
}
