import React from 'react';
import { Chart, LineAdvance, Axis } from 'bizcharts';
import textContext from '../../textContext';
import useUtils from '../../hooks/useUtils';
import { useApp } from '../../context/AppContext';

// Material UI
import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';

export default function Charts(props) {
  const { position, data, info } = props;
  const [alignment, setAlignment] = React.useState('day');
  const { language } = useApp();
  const { numberParse } = useUtils();
  const text = textContext[language].portfolio.chart;
  const positionInfo =
    position === 'anc'
      ? false
      : info.filter((pos) => pos.position === position)[0];
  const handleChange = (event) => {
    setAlignment(event.target.value);
  };
  const months = [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'November',
    'December',
  ];

  const toggleDay = () => {
    let dayArray = [];
    if (data && data.items) {
      for (let i = 21; i >= 0; i--) {
        let oldDay = new Date();
        oldDay.setDate(oldDay.getDate() - i - 1);
        let newDay = new Date();
        newDay.setDate(newDay.getDate() - i);
        let date =
          months[newDay.getMonth()].substring(0, 3) + ' ' + newDay.getDate();

        let oldTimestamp = oldDay.getTime() / 1000;
        let newTimestamp = newDay.getTime() / 1000;

        let filterData = data.items
          ? data.items.filter(
              (fil) =>
                fil.timestamp_sec >= oldTimestamp &&
                fil.timestamp_sec <= newTimestamp
            )
          : [];

        let values = [];
        filterData.forEach((d) => values.push(d.uusd_value));
        if (values.length === 0) {
          continue;
        }
        const value = values[values.length - 1];
        dayArray.push({ day: date, value: `${value / 1000000}` });
      }
    }
    return dayArray;
  };

  const toggleWeek = () => {
    let weekArray = [];
    for (let i = 17; i > 0; i--) {
      let newWeek = new Date();
      newWeek.setDate(newWeek.getDate() - (i - 1) * 7);
      let oldWeek = new Date();
      oldWeek.setDate(oldWeek.getDate() - i * 7);

      let date =
        months[newWeek.getMonth()].substring(0, 3) + ' ' + newWeek.getDate();

      let oldTimestamp = oldWeek.getTime() / 1000;
      let newTimestamp = newWeek.getTime() / 1000;

      let filterData = data.items
        ? data.items.filter(
            (fil) =>
              fil.timestamp_sec >= oldTimestamp &&
              fil.timestamp_sec <= newTimestamp
          )
        : [];

      let values = [];
      filterData.forEach((d) => values.push(d.uusd_value));
      if (values.length === 0) {
        continue;
      }
      const value = values[values.length - 1];
      weekArray.push({ week: date, value: `${value / 1000000}` });
    }
    return weekArray;
  };

  const toggleMonth = () => {
    let monthArray = [];
    for (let i = 11; i > 0; i--) {
      let newMonth = new Date();
      newMonth.setDate(newMonth.getDate() - (i - 1) * 31);
      let oldMonth = new Date();
      oldMonth.setDate(oldMonth.getDate() - i * 31);
      let date = months[newMonth.getMonth()].substring(0, 3);

      let oldTimestamp = oldMonth.getTime() / 1000;
      let newTimestamp = newMonth.getTime() / 1000;

      let filterData = data.items
        ? data.items.filter(
            (fil) =>
              fil.timestamp_sec >= oldTimestamp &&
              fil.timestamp_sec <= newTimestamp
          )
        : [];

      let values = [];
      filterData.forEach((d) => values.push(d.uusd_value));
      if (values.length === 0) {
        continue;
      }
      const value = values[values.length - 1];
      monthArray.push({ month: date, value: `${value / 1000000}` });
    }
    return monthArray;
  };

  return (
    <div className="chartClass">
      {position === 'anc' || positionInfo.closed ? (
        <div>
          <p>{text.title}</p>
          <span>{text.not}</span>
        </div>
      ) : (
        <React.Fragment>
          <div
            style={{
              display: 'flex',
              justifyContent: 'space-between',
              margin: '0 0 1rem 0',
            }}
          >
            <div style={{ margin: 'auto 0' }}>
              <h3>{text.title}</h3>
            </div>
            <div className="toggleWrapper">
              <ToggleButtonGroup
                className="toggleGroup"
                color="primary"
                value={alignment}
                exclusive
                onChange={handleChange}
              >
                <ToggleButton value="day">Day</ToggleButton>
                <ToggleButton value="week">Week</ToggleButton>
                <ToggleButton value="month">Month</ToggleButton>
              </ToggleButtonGroup>
            </div>
          </div>

          <Chart
            appendPadding={[20, 0, 40, 0]}
            autoFit
            height={500}
            data={
              alignment === 'day'
                ? toggleDay()
                : alignment === 'week'
                ? toggleWeek()
                : alignment === 'month'
                ? toggleMonth()
                : null
            }
            scale={{ value: { alias: 'Value', type: 'linear' } }}
          >
            <Axis
              name="value"
              label={{ formatter: (value) => `${numberParse(value)}` }}
            />
            <LineAdvance
              color="l (180) 0:#78CCFF 1:#672CFF"
              area
              position={`${alignment}*value`}
              shape="smooth"
              tooltip={[
                `${alignment}*value`,
                (alignment, value) => {
                  return {
                    name: 'value',
                    value: `${numberParse(value)}`,
                  };
                },
              ]}
            />
          </Chart>
        </React.Fragment>
      )}
    </div>
  );
}
