import React from 'react';
import textContext from '../../textContext';
import { useApp } from '../../context/AppContext';
import { useTheme } from '../../context/ThemeContext';

export default function AssetStrategy(props) {
  const { language } = useApp();
  const text = textContext[language].assets;
  const { strategyText } = text;

  const { assetImg } = useTheme();
  const { img1, img2, plus, title, chain } = props;

  return (
    <div className="strategy">
      <span className="textUpper">{strategyText}</span>
      <div className="assetCont">
        <div className="imgContainer">
          {plus ? (
            <img src={assetImg(title)} height="30" alt={title} />
          ) : (
            <>
              <img src={img1} height="30" alt={title} />
              <img src={img2} height="30" alt={title} />
            </>
          )}
        </div>
        <div>
          <div>
            <p>
              {plus && 'Delta-neutral-'}
              {title}
            </p>
            {plus && (
              <p className="fontOrange">
                <span>original</span>
              </p>
            )}
          </div>
          <span className="chainClass">{chain}</span>
        </div>
      </div>
    </div>
  );
}
