import React from 'react';
import { useApp } from '../../context/AppContext';
import textContext from '../../textContext';
import { ust } from '../../assets/images/icons';
import { lockIcon } from '../../assets/images';
import NumberFormat from 'react-number-format';
import AssetSearch from './AssetSearch';

// Material
import Popover from '@mui/material/Popover';
import InputBase from '@mui/material/InputBase';

export default function AssetSettings(props) {
  const {
    plus,
    invest,
    amount,
    setAmount,
    maxInvest,
    currentValue,
    withdrawValue,
    maxDeltaInvest,
  } = props;
  const { language } = useApp();
  const text = textContext[language].assets;
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [selectToken, setToken] = React.useState('ust');
  const {
    max,
    partialWithdrawl,
    tokenText,
    maxWithdraw,
    maxBalance,
    minInvest,
  } = text;
  const open = Boolean(anchorEl);
  const handleClose = () => {
    setAnchorEl(null);
  };
  const handleToken = (val) => {
    setToken(val);
  };

  const countDecimals = function (value) {
    if (Math.floor(value.valueOf()) === value.valueOf()) return 0;

    var str = value.toString();
    if (str.indexOf('.') !== -1 && str.indexOf('-') !== -1) {
      return str.split('-')[1] || 0;
    } else if (str.indexOf('.') !== -1) {
      return str.split('.')[1].length || 0;
    }
  };

  const handleNumber = (val) => {
    let parsed = val === '' ? 0 : parseFloat(val.replace(/,/g, ''));
    if (parsed >= 0 && countDecimals(parsed) < 7) {
      setAmount(parsed);
    }
  };

  return (
    <div
      className={
        (amount >= 2000 &&
          amount <=
            (invest && plus
              ? maxDeltaInvest
              : invest
              ? maxInvest
              : withdrawValue) &&
          plus) ||
        (!plus &&
          amount <=
            (invest && plus
              ? maxDeltaInvest
              : invest
              ? maxInvest
              : withdrawValue)) ||
        amount === '' ||
        amount === 0 ||
        (plus && currentValue)
          ? 'token'
          : 'token errorColor'
      }
    >
      <div aria-describedby="token-popover" className="selectToken">
        <div className="flex">
          <p className="textUpper">{tokenText}</p>
          {/*{ width < 1200 ? <ChevronRightIcon /> : <ArrowDown sx={{textAlign:'right'}} /> }*/}
        </div>
        <div
          className="flex"
          style={{ justifyContent: 'flex-start', alignItems: 'center' }}
        >
          {selectToken && <img src={ust} alt="ust" width="30" />}
          <h4 style={{ padding: '1px .5rem' }}>{selectToken}</h4>
        </div>
      </div>
      <Popover
        id="token-popover"
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
      >
        <div>
          <div className="flex">
            <p>Select Token</p>
            <div onClick={handleClose}>&#x2715;</div>
          </div>
          <AssetSearch plus={plus} close={handleClose} setToken={handleToken} />
        </div>
      </Popover>
      <div className={`selectValue ${!invest && plus ? 'withdraw' : ''}`}>
        {!invest && plus && <img src={lockIcon} alt="lock icon" />}
        <NumberFormat
          value={amount === 0 ? '' : amount === '' ? amount : amount}
          onChange={(event) => handleNumber(event.target.value)}
          customInput={InputBase}
          type="text"
          decimalScale={6}
          placeholder={'0.00'}
          thousandSeparator={true}
          disabled={currentValue ? true : false}
        />
        {!invest && plus && <p style={{ top: '5px' }}>{partialWithdrawl}</p>}
        {invest && (
          <span className="setMax">
            <button
              disabled={!invest && !withdrawValue}
              onClick={() =>
                setAmount(
                  withdrawValue
                    ? withdrawValue
                    : plus
                    ? maxDeltaInvest
                    : maxInvest
                )
              }
              className="textUpper"
            >
              {max}
            </button>
          </span>
        )}
        {(invest || !plus) && (
          <div className="flex">
            {!plus && amount > withdrawValue ? (
              <p className="alertText">
                {maxWithdraw}: {withdrawValue.toFixed(6)}
              </p>
            ) : !withdrawValue &&
              amount > (plus ? maxDeltaInvest : maxInvest) &&
              amount !== '' &&
              amount !== 0 ? (
              <p className="alertText">{maxBalance}</p>
            ) : plus && amount !== 0 && amount !== '' && amount < 2000 ? (
              <p className="alertText">{minInvest}</p>
            ) : (
              ''
            )}
          </div>
        )}
      </div>
    </div>
  );
}
