import React from 'react';
import { anchor } from '../../assets/images/icons';
import { useToken } from '../../context/TokenContext';
import { useApp } from '../../context/AppContext';
import { useTheme } from '../../context/ThemeContext';
import LinearProgress from '@mui/material/LinearProgress';
import styled from 'styled-components';
import useUtils from '../../hooks/useUtils';

const BorderLinearProgress = styled(LinearProgress)`
  width: 100%;
  height: 28px;
  opacity: 0.1;
  border-radius: 8px;
  position: relative;
  top: -3px;
`;

export default function Trade(props) {
  const { width } = useApp();
  const { exchange } = useToken();
  const { position } = props;

  const { assetImg } = useTheme();
  const { numberParse } = useUtils();

  const handleSelect = () => {
    props.handleSelect({
      position: props.anchor ? 'anc' : position.position,
      title: props.anchor ? 'Anchor' : `delta-neutral-${position.name}`,
    });
  };

  return (
    <React.Fragment>
      {props.anchor && (
        <div
          onClick={handleSelect}
          className={`wrapper ${props.selected && 'selected'}`}
        >
          <div className="trades">
            {width > 1199 && <img src={anchor} alt="anchor" width="40" />}
            <div className="content">
              <h4>Anchor Protocol</h4>
              {width > 1199 && <span>Terra</span>}
              <h4>
                {props.anchor &&
                  exchange &&
                  numberParse(
                    numberParse(props.anchor, 'small') * exchange.exchange_rate,
                    'currency'
                  )}
              </h4>
            </div>
          </div>
          {props.progress && (
            <React.Fragment>
              <hr className="progressHr" />
              <div className="progress">in progress</div>
            </React.Fragment>
          )}
        </div>
      )}
      {position && position.value && position.address && (
        <div
          onClick={handleSelect}
          className={`wrapper ${props.selected && 'selected'}`}
        >
          <div className="trades">
            {!position.name ? (
              <BorderLinearProgress />
            ) : (
              <React.Fragment>
                {width > 1199 && (
                  <img
                    src={assetImg(position.name)}
                    alt="delta-neutral ust"
                    width="40"
                  />
                )}
                <div className="content">
                  <h4>Delta-neutral-{position.name}</h4>
                  {width > 1199 && <span>Terra</span>}
                  <h4>{numberParse(position.value, 'currency')}</h4>
                </div>
              </React.Fragment>
            )}
          </div>
          {position.progress && (
            <React.Fragment>
              <hr className="progressHr" />
              <div className="progress">in progress</div>
            </React.Fragment>
          )}
        </div>
      )}
    </React.Fragment>
  );
}
