import { createContext, useContext, useState, useEffect } from 'react';
import * as AWS from 'aws-sdk';
import { useApp } from './AppContext';
import useUtils from '../hooks/useUtils';
import { useQueries } from './QueriesContext';
import { AnchorToken } from '@anchor-protocol/anchor.js';
import { useWallet } from '@terra-money/wallet-provider';
import { epochExchangeRate } from '../gql/hive/hiveGQL';
import { AnchorEarn, DENOMS, CHAINS } from '@anchor-protocol/anchor-earn';

const TokenContext = createContext();

export function useToken() {
  return useContext(TokenContext);
}

const TokenProvider = ({ children }) => {
  const { decimalFix, numberParse } = useUtils();
  const { status, network } = useWallet();
  const {
    setBalance,
    holderAddress,
    backendData,
    isMarketHourCondition,
  } = useApp();
  const {
    lcd,
    addressProvider,
    hiveClient,
    anchorEarn,
    userStatus,
    anchorNetwork,
  } = useQueries();

  const [error, setError] = useState();
  const [result, setResult] = useState();
  const [errMsg, setErrMsg] = useState();
  const [loading, setLoading] = useState();
  const [success, setSuccess] = useState();
  const [deposit, setDeposit] = useState();
  const [market, setMarketInfo] = useState();
  const [exchange, setExchange] = useState();
  const [progress, setProgress] = useState([]);
  const [anchorApy, setAnchorApy] = useState();
  const [anchorValue, setAnchorValue] = useState();
  const [tokenList, setTokenList] = useState(null);

  // mAsset
  const [mAssetsList, setMAssetsList] = useState([]);
  const [mAssetOracles, setMAssetOracles] = useState({});
  const [mAssetTradeFees, setMAssetTradeFees] = useState({});
  const [mAssetSpecAprs, setMAssetSpecAprs] = useState({});
  const [totalTvl, setTotalTvl] = useState('');
  // invest data
  const [investData, setInvestData] = useState([]);
  const [investPlus, setInvestPlus] = useState([]);
  const [investDataLookup, setInvestDataLookUp] = useState({});
  const [investPlusLookup, setInvestPlusLookup] = useState({});

  const getAnchor = () => {
    const anchorRequest = new AnchorToken(lcd, addressProvider);
    async function getInfo() {
      const marketInfo = await anchorEarn.market({
        currencies: [DENOMS.UST],
      });
      const getAnchorValue = await anchorRequest.getANCPrice();
      const collaterals = await fetch(
        `https://api${
          network.chainID === 'bombay-12' ? '-testnet' : ''
        }.anchorprotocol.com/api/v1/collaterals`
      )
        .then((response) => response.json())
        .then((data) => data);
      const deposits = await fetch(
        `https://api${
          network.chainID === 'bombay-12' ? '-testnet' : ''
        }.anchorprotocol.com/api/v1/deposit`
      )
        .then((response) => response.json())
        .then((data) => data);
      return { marketInfo, getAnchorValue, collaterals, deposits };
    }
    getInfo().then((data) => {
      const { marketInfo, collaterals, deposits, getAnchorValue } = data;
      setAnchorValue(getAnchorValue);
      setMarketInfo(marketInfo);
      setAnchorApy(data.marketInfo.markets[0].APY);
      const tokenData = {
        investPlus: [],
        invest: [
          {
            title: 'Anchor Protocol',
            action: true,
            tvl:
              numberParse(collaterals.total_value, 'small') +
              numberParse(deposits.total_ust_deposits, 'small'),
            anchorApr: data.marketInfo.markets[0].APY,
            ticker: 'anc',
            chain: 'terra',
            support: ['ust'],
            value: anchorValue,
            address: addressProvider.market(),
          },
        ],
      };
      setTokenList(tokenData);
    });
  };

  useEffect(() => {
    if (network && holderAddress && userStatus.connected) {
      const anchorEarn = new AnchorEarn({
        chain: CHAINS.TERRA,
        network: anchorNetwork.id,
        address: holderAddress,
      });
      const getUserToken = () => {
        async function getInfo() {
          const balanceInfo = await anchorEarn.balance({
            currencies: [DENOMS.UST],
          });
          return { balanceInfo, anchorEarn };
        }
        getInfo().then((data) => {
          const { total_account_balance_in_ust, total_deposit_balance_in_ust } =
            data.balanceInfo;
          setBalance(decimalFix(total_account_balance_in_ust));
          setDeposit(total_deposit_balance_in_ust);
        });
      };
      getUserToken();
    }
  }, [network, holderAddress, tokenList, status]); // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    if (network && lcd && !userStatus.initializing) {
      getAnchor();
    }
  }, [network, status, lcd]); // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    if (!!lcd && market) {
      let query = epochExchangeRate(addressProvider.market());
      const getExchange = () => {
        async function asyncExchange() {
          try {
            const [res] = await hiveClient.setQuery(query).request();
            return res;
          } catch (e) {
            console.error(e);
          }
        }
        asyncExchange().then((data) => {
          setExchange(data);
        });
      };
      getExchange();
    }
  }, [market, network, addressProvider, hiveClient, lcd]);

  // eslint-disable-line react-hooks/exhaustive-deps
  // Query mAsset TVL from DynamoDB and persist it into state.
  useEffect(() => {
    const dynamo = new AWS.DynamoDB({
      region: 'us-west-2',
    });
    const createTradeFeeLookup = (backendData) => {
      if (
        !backendData ||
        !backendData.trade_fee ||
        !backendData.trade_fee.length
      )
        return {};
      let tradeFeeLookup = {};
      backendData.trade_fee.forEach((x) => {
        tradeFeeLookup[x.token1] = x;
      });
      return tradeFeeLookup;
    };

    const getMAssets = async () => {
      const tradeFees = createTradeFeeLookup(backendData);
      if (tradeFees.length !== 0) setMAssetTradeFees(tradeFees);
      setMAssetSpecAprs(backendData.spec_apr);
      const assets = backendData.graphql_queries.assets;
      setMAssetOracles(backendData.oracle_queries);
      const keyName = 'strategy_id';
      const attribute = 'tvl_uusd';
      const tableName =
        network.chainID === 'bombay-12'
          ? 'latest_strategy_tvl_dev'
          : 'latest_strategy_tvl';
      let totalTvl = 0;

      let queryParams = {
        RequestItems: {
          [tableName]: {
            Keys: assets.map((item) => ({ [keyName]: { S: item.token } })),
            ProjectionExpression: `${keyName}, ${attribute}`,
          },
        },
      };
      try {
        const response = await dynamo.batchGetItem(queryParams).promise();
        if (response.Responses?.[tableName].length > 0) {
          response.Responses[tableName].forEach((item) => {
            const [strategyId, tvl] = [
              item[keyName].S,
              numberParse(item[attribute].S, 'small'),
            ];
            assets.filter((x) => x.token === strategyId)[0].tvl = tvl;
            totalTvl += tvl ?? 0;
          });
        }
      } catch (err) {
        console.error(err);
      }
      setTotalTvl(numberParse(totalTvl));
      setMAssetsList(assets);
    };

    if (!!backendData) {
      getMAssets();
    }
  }, [backendData, network]);  // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    if (tokenList !== null) {
      setInvestData(tokenList.invest);
      let tokenLookup = {};
      tokenList.invest.forEach((e, i) => {
        tokenLookup[e.address] = i;
      });
      setInvestDataLookUp(tokenLookup);
    }
  }, [tokenList]);

  // Use query data from above to calculate & populate for each row.
  useEffect(() => {
    let investPlusData = [];
    let investPlusLookupData = {};
    let investPlusDataIndex = 0;
    if (mAssetsList && market) {
      Object.entries(mAssetsList).forEach((d) => {
        const assetInfo = d[1];
        let obj = {};
        obj.address = assetInfo.token;
        obj.title = assetInfo.symbol;
        obj.name = assetInfo.name;
        obj.tvl = assetInfo.tvl || 0;
        const oracleInfo = mAssetOracles[assetInfo.token];

        // Some mAsset may not have oracle information due to error from
        // our backend API. If so, skip them for now.
        if (oracleInfo) {
          obj.action = isMarketHourCondition(oracleInfo.last_updated_base);
          let short = Number(assetInfo.statistic.apr.short);
          let long = Number(assetInfo.statistic.apr.long);

          // Anchor APY is actually APR, confirmed by Peiqian.
          let anchorApr = Number(market.markets[0].APY);
          let min_collateral = assetInfo.statistic.minCollateralRatio;
          const tradeFee =
            !!mAssetTradeFees && mAssetTradeFees[assetInfo.token]
              ? Number(mAssetTradeFees[assetInfo.token].apr)
              : 0;
          const specApr =
            !!mAssetSpecAprs && mAssetSpecAprs[assetInfo.symbol]
              ? Number(mAssetSpecAprs[assetInfo.symbol])
              : 0;

          let defaultCol = 1.8 * Number(min_collateral);
          const calcApr = (col = defaultCol) =>
            (() =>
              anchorApr * (col / (col + 1)) +
              (long + tradeFee + specApr) * (2 / (col + 1)) +
              short * (1 / (col + 1)))(anchorApr, tradeFee, specApr);

          obj.anchorApr = anchorApr;
          obj.defaultCol = defaultCol;
          obj.value = assetInfo.prices.price;
          obj.support = ['ust'];
          obj.short = short;
          obj.long = long;
          obj.chain = 'TERRA';
          obj.minCollateral = min_collateral;
          obj.APR = calcApr();
          obj.breakdown = {
            collateralApr: (col = defaultCol) =>
              (() => anchorApr * (col / (col + 1)))(anchorApr),
            shortFarmApr: (col = defaultCol) =>
              (() => short * (1 / (col + 1)))(short),
            longFarmApr: {
              spec: (col = defaultCol) =>
                (() => specApr * (2 / (col + 1)))(specApr),
              mir: (col = defaultCol) => (() => long * (2 / (col + 1)))(long),
              trade: (col = defaultCol) =>
                (() => tradeFee * (2 / (col + 1)))(tradeFee),
            },
            apr: calcApr,
          };
          investPlusData.push(obj);
          investPlusLookupData[assetInfo.token] = investPlusDataIndex++;
        }
      });
      setInvestPlusLookup(investPlusLookupData);
      setInvestPlus(investPlusData);
    }
  }, [market, mAssetsList]); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <TokenContext.Provider
      value={{
        error,
        errMsg,
        result,
        market,
        loading,
        success,
        deposit,
        exchange,
        progress,
        setError,
        getAnchor,
        tokenList,
        setResult,
        anchorApy,
        setErrMsg,
        setSuccess,
        setLoading,
        setProgress,
        mAssetsList,
        mAssetOracles,
        mAssetTradeFees,
        mAssetSpecAprs,
        totalTvl,
        investData,
        investPlus,
        investPlusLookup,
        investDataLookup,
      }}
    >
      {children}
    </TokenContext.Provider>
  );
};

export { TokenProvider, TokenContext };
