import React from 'react';
import styled from 'styled-components';
import InfoIcon from '@mui/icons-material/Info';
import textContext from '../../textContext';
import { useApp } from '../../context/AppContext';
import { useWallet } from '@terra-money/wallet-provider';

const Wrapper = styled('div')`
  background: #32c5ff;
  position: fixed;
  z-index: 20;
  top: 0;
  right: 0;
  padding: 0.25rem 0;
  height: 20px;
  width: calc(100% - 240px);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  small {
    margin: 0;
    padding: 0;
  }
  @media (max-width: 1199px) {
    top: 52px;
    width: 100%;
    small {
      font-size: 9px;
    }
    svg {
      font-size: 16px;
    }
  }
`;

const Close = styled('div')`
  margin: 0rem !important;
  position: absolute !important;
  right: 10px !important;
  top: 1px !important;
  cursor: pointer;
  @media (max-width: 1199px) {
    top: 0.5px !important;
    right: 5px !important;
  }
`;

export default function Banner() {
  const { status } = useWallet();
  const { language, isMarketHour } = useApp();
  const [market, setMarket] = React.useState(false);
  const [show, setShow] = React.useState(true);
  const text = textContext[language].banner;

  React.useEffect(() => {
    setMarket(isMarketHour);
  }, [isMarketHour]); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <React.Fragment>
      {status === 'WALLET_NOT_CONNECTED' && show && (
        <Wrapper>
          <InfoIcon sx={{ marginRight: 1 }} fontSize="small" />
          <small>{text.wallet}</small>
          <Close onClick={() => setShow(false)}>&#x2715;</Close>
        </Wrapper>
      )}
      {!market && status === 'WALLET_CONNECTED' && show && (
        <Wrapper>
          <InfoIcon sx={{ marginRight: 1 }} fontSize="small" />
          <small>{text.hours}</small>
          <Close onClick={() => setShow(false)}>&#x2715;</Close>
        </Wrapper>
      )}
    </React.Fragment>
  );
}
