import * as React from 'react';
import { useApp } from '../../context/AppContext';
import { useGas } from '../../context/GasContext';
import textContext from '../../textContext';
import AssetBreakdown from '../Investment/AssetBreakdown';
import useInvest from '../../hooks/useInvest';
import { useDelta } from '../../context/DeltaContext';
import useUtils from '../../hooks/useUtils';
import { useWallet } from '@terra-money/wallet-provider';
import { apertureTreasuryAddress } from '../../constants/contractAddresses';

// Material
import Slider from '@mui/material/Slider';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { useEffect, useState } from 'react';

export default function InvestAccordion(props) {
  const {
    plus,
    title,
    invest,
    breakdown,
    defaultCol,
    collateral,
    deltaFeeInit,
    minCollateral,
    anchorFeeInit,
    withdrawValue,
    handleCollateral,
    address,
    amount,
    position,
  } = props;
  const { language, holderAddress } = useApp();
  const { selectGas, gasFeeName, getGasFee } = useGas();
  const text = textContext[language].assets;
  const {
    feeText,
    feeTextWithdrawl,
    gasText,
    nonApertureFee,
    strategyFeeText,
    targetText,
    defaultCollateral,
    reminder1,
    reminder2,
    reminder3,
    reminder4,
  } = text;
  const { simulateInvest, deltaInvestMsg, deltaWithdrawSimulateMsg } =
    useInvest();
  const { posManager } = useDelta();
  const { numberParse } = useUtils();
  const [openPosNonApetureFee, setOpenPosNonApetureFee] = useState('');
  const [withdrawlNonApetureFee, setWithdrawlNonApetureFee] = useState('');
  const [withdrawlApertureFee, setWithdrawlApetureFee] = useState('');
  const [withdrawlGasFee, setWithdrawlGasFee] = useState(0);

  const { status, network } = useWallet();

  const increaseRatio = (collateral / 100 / minCollateral - 1) * 100;
  const color =
    increaseRatio < 50
      ? { color: '#FF4242' }
      : increaseRatio < 80
      ? { color: '#F48E00' }
      : { color: '#35F1C8' };

  useEffect(() => {
    const simulate = async () => {
      let encoding;
      if (plus) {
        let encode_data = plus && {
          target_min_collateral_ratio: (collateral / 100 - 0.2)
            .toFixed(2)
            .toString(),
          target_max_collateral_ratio: (collateral / 100 + 0.2)
            .toFixed(2)
            .toString(),
          mirror_asset_cw20_addr: address,
        };
        encoding = btoa(JSON.stringify(encode_data));
      }

      if (plus) {
        if (invest) {
          const deltaMsgs = deltaInvestMsg(encoding, amount);
          simulateInvest(deltaMsgs)
            .then((res) => {
              const log = JSON.parse(res.data.result.log);

              const events = log[0].events;

              const uusd = 'uusd';
              const coinReceivedLog = events.find(
                (ele) => ele.type === 'coin_received'
              ).attributes;
              var ustLongAmount = coinReceivedLog.slice(-1)[0].value;
              ustLongAmount = ustLongAmount.substring(
                0,
                ustLongAmount.length - uusd.length
              );
              var ustShortProceed = coinReceivedLog.slice(-3)[0].value;
              ustShortProceed = ustShortProceed.substring(
                0,
                ustShortProceed.length - uusd.length
              );

              const openPositionNonApertureFee =
                Number(ustLongAmount) - Number(ustShortProceed);

              setOpenPosNonApetureFee(openPositionNonApertureFee.toString());
            })
            .catch((err) => {
              console.log(err);
            });
        } else {
          const deltaMsgs = deltaWithdrawSimulateMsg(position, posManager);
          try {
            const gasFee = (await getGasFee(deltaMsgs)).toData().amount[0]
              .amount;
            setWithdrawlGasFee(numberParse(gasFee, 'small'));
          } catch (e) {
            console.log(e);
          }
          const beforeFeeValue = Number(amount);
          simulateInvest(deltaMsgs)
            .then((res) => {
              const treasuryAddress =
                status === 'WALLET_CONNECTED' && network.chainID === 'bombay-12'
                  ? apertureTreasuryAddress.bombay
                  : apertureTreasuryAddress.columbus;
              const withdrawLog = JSON.parse(res.data.result.log);
              const withdrawEvents = withdrawLog[0].events;
              const withdrawCoinReceivedLog = withdrawEvents.find(
                (ele) => ele.type === 'coin_received'
              ).attributes;
              // Note: aperture fee can be zero or not found. This happens if user doesn't
              // have any yield (i.e. they didn't make money).
              const apertureFeeIndex = withdrawCoinReceivedLog.findIndex(
                (ele) => ele.value === treasuryAddress
              );
              var apertureFee = undefined;

              if (apertureFeeIndex === -1) {
                apertureFee = Number(0);
              } else {
                const apertureFeeRaw =
                  withdrawCoinReceivedLog[apertureFeeIndex + 1].value;
                apertureFee = Number(
                  apertureFeeRaw.substring(0, apertureFeeRaw.length - 4)
                );
              }

              setWithdrawlApetureFee(apertureFee);

              // Step 3:
              //   Simulate to get non-Aperture fee. This could be Mirror's fee and potential swap fee.

              // The idea is as follows:
              //   non aperture fee = original prefee value - aperture fee - amount to be sent to user.
              //
              // We know the prefee value through the contract query and also calcualted the aperture fee above.
              // Now we only need to figure out the last remaining item: the amount to send to user.
              const userReceiveAmountIndex = withdrawCoinReceivedLog.findIndex(
                (ele) => ele.value === holderAddress
              );
              const userReceiveAmountRaw =
                withdrawCoinReceivedLog[userReceiveAmountIndex + 1].value;
              const userReceiveAmount = Number(
                userReceiveAmountRaw.substring(
                  0,
                  userReceiveAmountRaw.length - 4
                )
              );

              // Now we can calculate the non-aperture-fee.
              const nonApertureFee =
                numberParse(beforeFeeValue, 'big') -
                userReceiveAmount -
                apertureFee;
              setWithdrawlNonApetureFee(nonApertureFee);
            })
            .catch((err) => {
              console.log(err);
            });
        }
      }
    };

    if (invest && amount >= 2000) {
      simulate();
    } else if (!invest) {
      simulate();
    }
  }, [
    amount,
    invest,
    address,
    collateral,
    deltaInvestMsg,
    deltaWithdrawSimulateMsg,
    holderAddress,
    network.chainID,
    numberParse,
    plus,
    posManager,
    position,
    simulateInvest,
    status,
    getGasFee,
  ]);
  return (
    <Accordion defaultExpanded={invest ? false : true} className="feesClass">
      <AccordionSummary
        expandIcon={<ExpandMoreIcon className="expandIcon" />}
        id="fees-header"
        className={!plus ? 'hiddenPlus' : ''}
      >
        <p className="title">
          {!plus ? (
            <span>
              {gasText}: {anchorFeeInit}{' '}
              <span style={{ textTransform: 'uppercase' }}>
                {gasFeeName(selectGas.name)}
              </span>
            </span>
          ) : invest ? (
            feeText
          ) : (
            feeTextWithdrawl
          )}
        </p>
      </AccordionSummary>
      {plus && (
        <AccordionDetails className="feesDetails">
          <p className="flex">
            <span className="colorGray">{gasText}</span>
            <span className="textPrimary">
              {plus && !withdrawValue && invest && numberParse(deltaFeeInit)}
              {plus && !invest && withdrawlGasFee} {gasFeeName(selectGas.name)}
            </span>
          </p>
          <p className="flex">
            <span className="colorGray">{nonApertureFee}</span>
            <span className="textPrimary">{`${numberParse(
              invest ? openPosNonApetureFee : withdrawlNonApetureFee,
              'small'
            )} UST`}</span>
          </p>
          <p
            className="flex space hrLine"
            style={!invest ? { marginBottom: '0' } : { marginBottom: '1rem' }}
          >
            <span className="colorGray">{strategyFeeText}</span>
            <span className="textPrimary">
              {invest
                ? `10%`
                : `${numberParse(withdrawlApertureFee, 'small')} UST`}
            </span>
          </p>
          {invest && (
            <>
              <hr className="accordionHr" />
              <p className="space target">
                <p className="title">{targetText}</p>
                <div className="flex">
                  <Slider
                    sx={color}
                    className="slider"
                    onChange={handleCollateral}
                    aria-label="collateral"
                    value={collateral}
                    valueLabelDisplay="auto"
                    step={1}
                    min={Number(minCollateral) * 100 + 50}
                    max={Number(minCollateral) * 100 + 250}
                  />
                  <div className="collateralClass">{collateral}%</div>
                  {Number(collateral) === Number(defaultCol) && (
                    <div className="collateralIndicator">
                      {defaultCollateral}
                    </div>
                  )}
                </div>
              </p>
              <p className="space hrLine">
                <span className="colorGray">{reminder1}</span>
                {title}
                <span className="colorGray">{reminder2}</span>
                <span style={color}>{increaseRatio.toFixed(0)}%</span>
                <span className="colorGray">{reminder3}</span>
                {defaultCol}%<span className="colorGray">{reminder4}</span>
              </p>
              <hr className="accordionHr" />
              <AssetBreakdown
                breakdown={breakdown}
                collateralRatio={collateral / 100}
              />
            </>
          )}
        </AccordionDetails>
      )}
    </Accordion>
  );
}
