import React from 'react';
import { useApp } from '../../context/AppContext';
import textContext from '../../textContext';

export default function Notification() {
  const { language } = useApp();
  const text = textContext[language].actionRequired;
  const [state, setState] = React.useState(false);

  const handleAccept = () => {
    document.body.style.overflow = 'auto';
    setState(true);
  };

  function WarningIcon(props) {
    return (
      <svg
        width={73}
        height={74}
        viewBox="0 0 73 74"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
        {...props}
      >
        <path
          fillRule="evenodd"
          clipRule="evenodd"
          d="M39.818 26.559v16.6h-6.64v-16.6h6.64zm-3.32 24.899a3.32 3.32 0 013.32 3.32v1.66a3.32 3.32 0 11-6.64 0v-1.66a3.32 3.32 0 013.32-3.32z"
          fill="#FF8629"
        />
        <path
          fillRule="evenodd"
          clipRule="evenodd"
          d="M39.02 8.315c-1.133-2.232-3.905-2.236-5.042.002L7.09 61.19c-.02.043-.04.085-.062.128-1.321 2.596.635 5.08 2.52 5.08H63.44c1.886 0 3.844-2.484 2.528-5.084L39.02 8.315zM1.035 58.461c.023-.052.047-.103.073-.154l26.95-52.998c3.597-7.076 13.287-7.081 16.882 0L71.89 58.31c3.203 6.323-.854 14.727-8.45 14.727H9.548c-7.528 0-11.59-8.268-8.513-14.577z"
          fill="#FF8629"
        />
      </svg>
    );
  }

  return (
    <React.Fragment>
      {!state && (
        <div className="notification-modal">
          <div className="wrapper">
            <WarningIcon />
            <h2>{text.header}</h2>
            <div className="text">
              <p>
                {text.text1}{' '}
                <b>{text.text2}</b>{' '}
                {text.text3}
              </p>
              <p>{text.text4}</p>
            </div>
            <div className="confirm">
              <button onClick={handleAccept} className={'buttonColor'}>
                <h3>{text.confirm}</h3>
              </button>
            </div>
          </div>
        </div>
      )}
    </React.Fragment>
  );
}
