import React from 'react';
import styled from 'styled-components';
import textContext from '../../textContext';
import { useApp } from '../../context/AppContext';
import { error } from '../../assets/images';
import Modal from '@mui/material/Modal';

const Close = styled('div')`
  position: absolute;
  right: 0;
  top: 0;
  height: 19px;
  width: 19px;
  color: white;
  font-size: 22px;
  font-weight: 500;
  padding: 0.5rem;
  cursor: pointer;
`;

export default function MirrorErrorModal() {
  const { language, mirrorError, setMirrorError } = useApp();
  const text = textContext[language].assets.mirrorError;
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => {
    setOpen(false);
    setMirrorError();
  };

  React.useEffect(() => {
    mirrorError && handleOpen();
  }, [mirrorError]);

  return (
    <React.Fragment>
      <Modal id="errorModal" open={open} onClose={handleClose}>
        <div className="errorModalContainer">
          <Close onClick={handleClose}>&#x2715;</Close>
          <img src={error} alt="mirrorError" width="90" height="90" />
          <h3>{text.title}</h3>
          <h4>
            {text.text1} <u>{text.text2}</u> {text.text3}
          </h4>
          <p>
            {text.subtext1}{' '}
            <a href={text.link1} target="_blank" rel="noreferrer">
              <span className="highlight">{text.subtext2}</span>
            </a>{' '}
            {text.subtext3}{' '}
            <a href={text.link1} target="_blank" rel="noreferrer">
              <span className="highlight">{text.subtext4}</span>
            </a>{' '}
            {text.subtext5}
          </p>
        </div>
      </Modal>
    </React.Fragment>
  );
}
