import textContext from '../../textContext';
import { useApp } from '../../context/AppContext';
import { useWallet } from '@terra-money/wallet-provider';
import { useDelta } from '../../context/DeltaContext';
import { Link } from 'react-router-dom';
import ChevronRight from '@mui/icons-material/ChevronRight';
import useUtils from '../../hooks/useUtils';
import PropTypes from 'prop-types';

const Portfolio = (props) => {
  const { language } = useApp();
  const { deltaSum, initialSum } = useDelta();
  const { status } = useWallet();

  const { numberParse } = useUtils();
  const text = textContext[language].investment.portfolio;

  let formatter = new Intl.NumberFormat('en-US');

  return (
    <div className="wrapper">
      {props.type === 'tvl' ? (
        <span className="portClass">
          <p className="title">{text.TVL}</p>
          <h2>{props.totalTvl || ' - -'}</h2>
        </span>
      ) : (
        <div>
          <span className="portClass">
            <p className="title">{text.title}</p>
          </span>
          <span className="portClass">
            <p className="subTitle">{text.list.total}</p>

            <h2>
              {status === 'WALLET_CONNECTED' && deltaSum
                ? numberParse(deltaSum, 'currency')
                : ' - -'}
            </h2>
          </span>
          <span className="portClass">
            <p className="subTitle">{text.list.original}</p>
            <h3>
              {status === 'WALLET_CONNECTED' && initialSum
                ? numberParse(initialSum, 'currency')
                : ' - -'}
            </h3>
          </span>
          <span className="portClass">
            <p className="subTitle">{text.list.historical}</p>
            <h3 style={{ color: '#81FF8D' }}>
              {status === 'WALLET_CONNECTED' && initialSum && deltaSum
                ? `${deltaSum - initialSum > 0 ? '+' : ''}${formatter.format(
                    deltaSum - initialSum
                  )} UST`
                : ' - - UST'}
            </h3>
          </span>
          <Link to="/portfolio" className="linkClass">
            View Details <ChevronRight />
          </Link>
        </div>
      )}
    </div>
  );
};

export default Portfolio;

Portfolio.defaultProps = {
  type: '',
  totalTvl: '',
};

Portfolio.propTypes = {
  type: PropTypes.string,
  totalTvl: PropTypes.string,
};
