import React from 'react';
import { useApp } from '../../context/AppContext';
import textContext from '../../textContext';
import ConnectDialog from './ConnectDialog';
import styled from 'styled-components';
import { useWallet, WalletStatus } from '@terra-money/wallet-provider';

// Material
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import ChevronLeft from '@mui/icons-material/ChevronLeft';

const Close = styled('span')`
  float: right;
  cursor: pointer;
  font-size: 2rem;
  font-weight: 200;
`;

export default function Connect() {
  const { language } = useApp();
  const text = textContext[language].navbar;
  const [open, setOpen] = React.useState(false);
  const [value, setValue] = React.useState(false);
  const [metaInstalled] = React.useState(false);
  const [title, setTitle] = React.useState(text.connect.dialog);

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
    setTimeout(() => {
      setValue(false);
      setTitle(text.connect.dialog);
    }, 500);
  };
  const handleTitle = () => {
    if (title === text.connect.dialog) {
      setTitle(text.connect.auth);
      setValue(true);
    } else if (title === text.connect.auth) {
      setTitle(text.connect.dialog);
      setValue(false);
    }
  };

  const { status } = useWallet();

  const classes = {
    float: 'left',
    fontSize: '30px',
    cursor: 'pointer',
  };

  return (
    <React.Fragment>
      <button
        onClick={handleClickOpen}
        className={`buttonColor ${
          status === WalletStatus.INITIALIZING ? 'initClass' : ''
        }`}
        disabled={status === WalletStatus.INITIALIZING}
      >
        {text.connect.button}
      </button>
      <Dialog
        open={open}
        onClose={handleClose}
        maxWidth="sm"
        fullWidth
        id="connectDialog"
      >
        <DialogTitle className="connectTitle">
          {title !== text.connect.dialog && (
            <ChevronLeft onClick={handleTitle} sx={classes} />
          )}
          <p>{title}</p>
          <Close onClick={handleClose}>&times;</Close>
        </DialogTitle>
        <DialogContent className="connectBody">
          <ConnectDialog
            metaInstalled={metaInstalled}
            title={title}
            value={value}
            click={handleTitle}
          />
        </DialogContent>
      </Dialog>
    </React.Fragment>
  );
}
