import React from 'react';
import { useWallet, WalletStatus } from '@terra-money/wallet-provider';
import Spinner from './Spinner';
import { isMobile } from 'react-device-detect';

const Card = (props) => {
  return (
    <div
      onClick={props.click}
      className={`connectCard ${props.width ? 'spinner' : ''}`}
    >
      <div>
        <img width="40" src={props.img} alt={props.text} />
        <p>{props.text}</p>
      </div>
      {props.loading && <Spinner />}
    </div>
  );
};

export default function ConnectDialog(props) {
  const [element, setElement] = React.useState();
  let terraIcon = 'https://assets.terra.money/icon/station-extension/icon.png';

  const {
    status,
    availableInstallTypes,
    availableConnections,
    connect,
    install,
  } = useWallet();

  const handleInstall = (val, icon) => {
    install(val);
    let elCard = <Card width loading text="Install Terra Station" img={icon} />;
    setElement(elCard);
    props.click();
  };

  const handleConnect = (x, y, name, icon) => {
    connect(x, y);
    let elCard = <Card width loading text={name} img={icon} />;
    setElement(elCard);
    props.click();
  };

  return (
    <div className="connectContainer">
      {element && props.value && element}
      {!props.value &&
        (status === WalletStatus.WALLET_NOT_CONNECTED ||
          status === WalletStatus.INITIALIZING) && (
          <React.Fragment>
            {availableInstallTypes.map(
              (connectType) =>
                !isMobile && (
                  <Card
                    key={'install-' + connectType}
                    click={() => handleInstall(connectType, terraIcon)}
                    text="Install Terra Station"
                    img={terraIcon}
                  />
                )
            )}
            {availableConnections
              .filter((f) => f.type !== 'READONLY')
              .map(({ type, name, icon, identifier = '' }) =>
                isMobile && type === 'WALLETCONNECT' ? (
                  <Card
                    key={'connection-' + type + identifier}
                    click={() => handleConnect(type, identifier, name, icon)}
                    text={name}
                    img={icon}
                  />
                ) : (
                  !isMobile && (
                    <Card
                      key={'connection-' + type + identifier}
                      click={() => handleConnect(type, identifier, name, icon)}
                      text={name}
                      img={icon}
                    />
                  )
                )
              )}
          </React.Fragment>
        )}
    </div>
  );
}
