import textContext from '../../textContext';
import moment from 'moment';
import { useApp } from '../../context/AppContext';
import { useToken } from '../../context/TokenContext';
import useUtils from '../../hooks/useUtils';
import { useEffect, useState } from 'react';
import { Grid, Box } from '@mui/material';

export default function Value(props) {
  const { language } = useApp();
  const { exchange } = useToken();
  const { numberParse } = useUtils();
  const [apr, setApr] = useState();
  const text = textContext[language].portfolio.asset;

  const histoicalCalc = () => {
    const { value, initial } = props;
    const calc = numberParse(value - initial);
    const percent = numberParse(((value - initial) / initial) * 100);
    return { calc, percent };
  };

  useEffect(() => {
    if (!props.loading && props.initial && props.time && props.value) {
      const [cur, ini] = [props.value, props.initial];
      let today = new Date().getTime() / 1e3;
      let momentInvestDate = moment.unix(props.time / 1e9);
      let momentToday = moment.unix(today);
      let diffDay = momentToday.diff(momentInvestDate, 'days', true);

      let diff = cur - ini;
      let dailyRate = diff / ini / diffDay;
      let investAPR =
        dailyRate < 0 ? '--' : `${(dailyRate * 365 * 100).toFixed(2)}%`;
      setApr(investAPR);
    }
  }, [props.initial, props.value, props.time, props.loading]);

  return (
    <div className="valueClass">
      <Box m={{ lg: 'auto' }} sx={{ flexGrow: 1 }}>
        <Grid
          sx={props.anchor ? {} : { my: 2 }}
          justifyContent={props.anchor ? '' : 'flex-start'}
          direction={{ xs: 'column', lg: 'row' }}
          className="valueClass__container"
          container
        >
          <Grid
            container
            item
            mt={props.position !== 'anc' ? { lg: -3 } : {}}
            xs={12}
            md={3}
            lg={4}
          >
            <div style={{ margin: 'auto 55px auto 0' }}>
              <span className="textUpper" style={{ fontSize: '18px' }}>
                {text.value}
              </span>
              {props.anchor && exchange ? (
                <h2 style={{ fontSize: '30px' }}>
                  {numberParse(
                    numberParse(props.value, 'small') *
                      exchange['exchange_rate'],
                    ''
                  )}
                </h2>
              ) : (
                <h2 style={{ fontSize: '30px' }}>
                  {props.value && numberParse(props.value, '')}
                </h2>
              )}
            </div>
          </Grid>
          {/* {props.position !== 'anc' && <hr />} */}
          {props.position !== 'anc' && (
            <>
              <Grid container item xs={12} md={3} lg={2}>
                <div style={{ minWidth: '93px', margin: 'auto 55px auto 0' }}>
                  <span className="textUpper">{text.original}</span>
                  <h3 style={{ marginTop: '2px' }}>
                    {!props.loading &&
                      props.initial &&
                      numberParse(props.initial, '')}
                    {props.loading && '--'}
                  </h3>
                </div>
              </Grid>
              <Grid container item xs={12} md={3} lg={2}>
                <div style={{ margin: 'auto 55px auto 0' }}>
                  <span className="textUpper">{text.historical}</span>
                  <h3 style={{ color: '#81FF8D', marginTop: '2px' }}>
                    {props.value &&
                      !props.loading &&
                      props.initial &&
                      `+${histoicalCalc().calc}`}{' '}
                    {props.loading && '--'}
                  </h3>
                </div>
              </Grid>
              <Grid container item xs={12} md={3} lg={2}>
                <div style={{ margin: 'auto 0' }}>
                  <span className="textUpper">{text.apr}</span>
                  <>
                    <h3 style={{ color: '#81FF8D', marginTop: '2px' }}>
                      {!props.loading && props.initial && props.value && apr}
                      {props.loading && '--'}
                    </h3>
                  </>
                </div>
              </Grid>
            </>
          )}
          {/* </div> */}
        </Grid>
      </Box>
    </div>
  );
}
