import React from 'react';
import { copy } from '../../assets/images';
import HorizIcon from '@mui/icons-material/MoreHoriz';
import { useApp } from '../../context/AppContext';
import { useWallet } from '@terra-money/wallet-provider';
import useUtils from '../../hooks/useUtils';

// Material
import Popover from '@mui/material/Popover';
import MenuItem from '@mui/material/MenuItem';
import CheckIcon from '@mui/icons-material/Check';
import Tooltip from '@mui/material/Tooltip';
import SwapHoriz from '@mui/icons-material/SwapHoriz';

export default function AuthUser(props) {
  const [copied, setCopied] = React.useState(false);
  const [icon, setIcon] = React.useState();
  const [num, setNum] = React.useState(1);
  const { token } = props;
  const { numberParse } = useUtils();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  const strSlice = (i, j) => token && token.slice(i, j);

  const getIcon = async (val) => {
    if (!val) {
      num === 5 ? setNum(1) : setNum(num + 1);
    }
    fetch(`https://robohash.org/${token}?set=set${val ? val : num}`)
      .then((response) => {
        setIcon(response.url);
        !val && localStorage.setItem('roboSet', num);
      })
      .catch((error) => {
        console.error('Error:', error);
      });
  };

  React.useEffect(() => {
    let getRobo = localStorage.getItem('roboSet');
    getRobo !== null ? getIcon(getRobo) : getIcon();
  }, [token]); // eslint-disable-line react-hooks/exhaustive-deps

  const { disconnect } = useWallet();
  const { width, balance, deleteToken, setBackendDataIsDirty } = useApp();

  const handleCopy = () => {
    setCopied(true);
    navigator.clipboard.writeText(token);
    setTimeout(function () {
      setCopied(false);
    }, 2000);
  };

  const handleDisconnet = () => {
    disconnect();
    deleteToken();
    setBackendDataIsDirty(true);
  };

  const handleChange = () => {
    num === 4 ? setNum(1) : setNum(num + 1);
    getIcon();
  };

  return (
    <div className="authUser">
      <div className="authIcon">
        {icon && (
          <>
            <img src={icon} alt="user icon" />
            <SwapHoriz className="switchIcon" onClick={handleChange} />
          </>
        )}
      </div>
      <div className="authBody">
        <h4>{!balance ? '--' : numberParse(balance, 'currency')}</h4>
        {token && (
          <div>
            <p>{`${strSlice(0, 4)}...${strSlice(-4)}`}</p>
            <span onClick={handleCopy}>
              {copied ? (
                <React.Fragment>
                  <Tooltip title="Copied" placement="top" open arrow>
                    <CheckIcon sx={{ width: 20, height: 18 }} />
                  </Tooltip>
                </React.Fragment>
              ) : (
                <React.Fragment>
                  <img src={copy} alt="copy token" />
                </React.Fragment>
              )}
            </span>
            <HorizIcon onClick={handleClick} className="authOption" />
          </div>
        )}
      </div>
      <Popover
        id="dropdown-menu"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        anchorOrigin={
          width > 1199
            ? { vertical: 'bottom', horizontal: 'left' }
            : { vertical: 'bottom', horizontal: 'right' }
        }
        transformOrigin={
          width > 1199
            ? { vertical: 'top', horizontal: 'right' }
            : { vertical: 'top', horizontal: 'center' }
        }
      >
        <MenuItem onClick={handleDisconnet}>Disconnect</MenuItem>
      </Popover>
    </div>
  );
}
