import React from 'react';
import { ust, luna, anchor, aUST, spec, mir } from '../../assets/images/icons';
import { useApp } from '../../context/AppContext';
import { useDelta } from '../../context/DeltaContext';
import useUtils from '../../hooks/useUtils';

const Holdings = (props) => {
  const { balance } = useApp();
  const { numberParse } = useUtils();
  const handleImg = (img) => {
    switch (img) {
      case 'ust':
        return ust;
      case 'uluna':
        return luna;
      case 'anc':
        return anchor;
      case 'aust':
        return aUST;
      case 'spec':
        return spec;
      case 'mir':
        return mir;
      default:
        return null;
    }
  };

  return (
    <div
      className={`appInvest token ${props.anchor ? 'anchorInvestClass' : ''}`}
    >
      <div>
        <div className="flex">
          <img src={handleImg(props.token)} alt="token" />
          <p>{props.token === 'uluna' ? 'luna' : props.token}</p>
        </div>
      </div>
      <div>
        {props.token === 'ust' ? (
          <p>{balance ? numberParse(balance) : 0}</p>
        ) : (
          <p>~ {numberParse(props.amount, 'small').toLocaleString()} UST</p>
        )}
      </div>
    </div>
  );
};

export default function TokenHoldings(props) {
  const { numberParse } = useUtils();
  const { balances, coinBalances } = useDelta();
  const [coinList, setCoins] = React.useState([]);
  const [balanceList, setBalance] = React.useState([]);

  React.useEffect(() => {
    if (balances.length === 5 && coinBalances.length <= 2) {
      setBalance(balances);
      setCoins(coinBalances);
    }
  }, [balances, coinBalances]);

  return (
    <div className="tokenHoldings">
      {balanceList
        .filter((f) => numberParse(f.amount, 'small') > 0.01)
        .map((l, i) => (
          <Holdings
            key={i}
            img={l.img}
            token={l.token}
            amount={l.amount}
            anchor={l.anchor}
          />
        ))}
      {coinList
        .filter((f) => numberParse(f.amount, 'small') > 0.01)
        .map((l, i) => (
          <Holdings
            key={i}
            img={l.img}
            token={l.token}
            amount={l.amount}
            anchor={l.anchor}
          />
        ))}
    </div>
  );
}
