import { Fragment, useState } from 'react';
import Grid from '@mui/material/Grid';
import Asset from '../../pages/Asset';
import { useNavigate } from 'react-router-dom';
import { anchor, terra } from '../../assets/images/icons';
import { useWallet } from '@terra-money/wallet-provider';
import { useApp } from '../../context/AppContext';
import { useTheme } from '../../context/ThemeContext';
import useUtils from '../../hooks/useUtils';
import Dropdown from './Dropdown';
import InfoIcon from '@mui/icons-material/Info';
import textContext from '../../textContext';
import TableAssetsTooltip from './TableAssetsTooltip';
import AssetBreakdown from './AssetBreakdown';

export default function TableAssets(props) {
  const { address, token, chain, APR, tvl, anchorApr, name, title, breakdown } =
    props.data;

  const { status } = useWallet();
  const { numberParse } = useUtils();
  const { assetImg } = useTheme();
  const { language, width } = useApp();
  const [open, setOpen] = useState(false);
  const [drop, setDrop] = useState(false);
  const [tooltipIsActive, setToolTipIsActive] = useState(false);
  const investText = textContext[language].investment.invest;
  const tableAssetsText = investText.tableAssets;
  const tooltipText = tableAssetsText.tooltip;

  const handleTooltipOpen = () => {
    setToolTipIsActive(true);
  };
  const handleTooltipClose = () => {
    setToolTipIsActive(false);
  };
  const handleClose = () => {
    setOpen(false);
    navigate('/strategy');
  };

  const navigate = useNavigate();
  const handleInvest = (e, inv) => {
    e.stopPropagation();
    navigate(`/strategy/asset&=${address}`);
    setOpen(true);
    props.isOpen(true);
  };

  const handleAccordion = () => {
    if (!tooltipIsActive) setDrop(!drop);
  };

  const accordionClose = () => {
    setDrop(false);
  };

  return (
    <Fragment>
      <Grid
        onClick={handleAccordion}
        className={`${drop ? 'active' : ''} tableAssets`}
        container
      >
        <Grid my={{ lg: 'auto' }} item xs={10} md={6} order={{ xs: 1 }}>
          <span className="mobileHeader" style={{ marginLeft: '3rem' }}>
            {investText.order[0].text}
          </span>
          <div className="assetCont">
            <div className="imgContainer">
              {props.plus ? (
                <img src={assetImg(title)} height="35" alt={token} />
              ) : (
                <div className="imgClass">
                  <img src={terra} height="30" alt={token} />
                  <img src={anchor} height="30" alt={token} />
                </div>
              )}
            </div>
            <div>
              <div>
                <p>
                  {props.plus && 'Delta-neutral-'}
                  {title}
                </p>
                {props.plus && (
                  <p className="fontOrange">
                    <span>{tableAssetsText.investPlus}</span>
                  </p>
                )}
              </div>
              <span className="chainClass">{chain}</span>
            </div>
          </div>
        </Grid>
        <Grid
          my={{ lg: 'auto' }}
          item
          xs={12}
          md={3}
          order={{ xs: 4, md: 2 }}
          className="gridEdit"
          style={!props.plus && width < 900 ? { paddingLeft: '3rem' } : {}}
        >
          <span className="mobileHeader"> {investText.order[1].text}</span>
          {props.data.tvl ? `${numberParse(tvl, 'table')}` : '-'}
        </Grid>
        <Grid
          my={{ lg: 'auto' }}
          item
          xs={12}
          md={1}
          order={{ xs: 3, md: 3 }}
          className="gridEdit"
          style={!props.plus && width < 900 ? { paddingLeft: '3rem' } : {}}
        >
          <span className="mobileHeader"> {investText.order[2].text}</span>
          <Grid container wrap="nowrap" direction="row">
            <Grid item>
              {APR && `${numberParse(APR * 100)}%`}
              {!props.plus && `${numberParse(anchorApr * 100)}%`}
            </Grid>
            <Grid
              sx={{
                zIndex: '10',
                cursor: 'help',
              }}
              item
            >
              {props.plus && (
                <TableAssetsTooltip
                  onOpen={handleTooltipOpen}
                  onClose={handleTooltipClose}
                  title={
                    <Grid
                      px={1}
                      py={1}
                      container
                      wrap="nowrap"
                      direction="column"
                      alignItems="center"
                    >
                      <Grid pb={1} sx={{ width: '100%' }} item>
                        <AssetBreakdown breakdown={breakdown} />
                      </Grid>
                      <Grid sx={{ maxWidth: 'fit-content!important' }} item>
                        <ol className="subtext">
                          {tooltipText.notes.map((x, i) => (
                            <li key={`tooltip-notes-${i}`}>
                              {i === 0 &&
                                textContext.utils.stringReplacer(
                                  {
                                    ratio: `${numberParse(
                                      (props.data.defaultCol /
                                        (props.data.defaultCol + 1)) *
                                        100
                                    )}%`,
                                    ancApr: `${numberParse(
                                      props.data.anchorApr * 100
                                    )}%`,
                                    ancTimesRatio: `${numberParse(
                                      props.data.anchorApr *
                                        (props.data.defaultCol /
                                          (props.data.defaultCol + 1)) *
                                        100
                                    )}%`,
                                  },
                                  x
                                )}
                              {i !== 0 && x}
                            </li>
                          ))}
                        </ol>
                      </Grid>
                    </Grid>
                  }
                  placement="bottom-start"
                >
                  <InfoIcon
                    sx={{
                      width: '0.6em',
                      height: 'auto',
                      margin: 'auto',
                      paddingTop: '0.1em',
                    }}
                  />
                </TableAssetsTooltip>
              )}
            </Grid>
          </Grid>
        </Grid>
        <Grid
          my={{ lg: 'auto' }}
          className="investButton"
          item
          xs={2}
          md={2}
          order={{ xs: 2, md: 4 }}
        >
          <button
            disabled={status !== 'WALLET_CONNECTED' || !props.data.action || props.plus}
            className={
              status !== 'WALLET_CONNECTED' || !props.data.action || props.plus
                ? 'disabled'
                : ''
            }
            onClick={(event) => handleInvest(event, address)}
          >
            {investText.action}
          </button>
        </Grid>
        <Grid item xs={12} order={{ xs: 5 }}>
          {drop && (
            <Dropdown
              name={name}
              title={title}
              accordionClose={accordionClose}
              data={props.info}
            />
          )}
        </Grid>
        <Grid xs={12} item order={{ xs: 10 }}>
          <hr className="tableHr" />
        </Grid>
      </Grid>
      {open && (
        <Asset
          invest
          data={props.data}
          img1={terra}
          img2={anchor}
          plus={props.plus}
          token={address}
          close={handleClose}
        />
      )}
    </Fragment>
  );
}
