export function wasmQuery(contractAddress: string, query: object) {
  return { contractAddress, query };
}
/** Query constructors */
export function strategyMetadataQuery(
  contractAddress: string,
  strategyId: number
) {
  const query = {
    get_strategy_metadata: {
      strategy_id: `${strategyId}`,
    },
  };
  return wasmQuery(contractAddress, query);
}

export function positionByHolderQuery(
  contractAddress: string,
  holderAddress: string,
  limit: number,
  start?: string
) {
  const query = {
    get_terra_positions_by_holder: {
      holder: holderAddress,
      limit,
      start_after: !!start ? `${start}` : undefined,
    },
  };
  return wasmQuery(contractAddress, query);
}

export function tokenBalanceQuery(
  contractAddress: string,
  holderAddress: string
) {
  return wasmQuery(contractAddress, {
    balance: { address: holderAddress },
  });
}

// Return aTerra exchange rate
export function epochExchangeRate(contractAddress: string) {
  return wasmQuery(contractAddress, {
    epoch_state: {},
  });
}

// Return object containing gql query and key to access the hive gql request results
export const positionInfo = (
  aTerraContractAddr: string,
  terraManagerAddr: string,
  holderAddress: string,
  positionLimit: number,
  strategyId: number
) => {
  return [
    tokenBalanceQuery(aTerraContractAddr, holderAddress),
    positionByHolderQuery(terraManagerAddr, holderAddress, positionLimit),
    strategyMetadataQuery(terraManagerAddr, strategyId),
  ];
};

export const positionAddress = (
  contractAddress: string,
  positionId: string,
  chainId: number
) => {
  const query = {
    get_position_contract_addr: {
      position: {
        chain_id: chainId,
        position_id: positionId,
      },
    },
  };
  return wasmQuery(contractAddress, query);
};

export const positionDetails = (contractAddress: string) => {
  const query = {
    get_position_info: {},
  };
  return wasmQuery(contractAddress, query);
};

export const swapAddress = (
  contractAddress: string,
  amount: string,
  operation: string,
  asset: string,
  denom: string
) => {
  const terraSwap = {
    terra_swap: {
      offer_asset_info: { token: { contract_addr: asset } },
      ask_asset_info: { native_token: { denom: denom } },
    },
  };
  const nativeSwap = {
    native_swap: {
      offer_denom: asset,
      ask_denom: denom,
    },
  };
  const query = {
    simulate_swap_operations: {
      offer_amount: amount,
      operations: [
        operation === 'terra'
          ? terraSwap
          : operation === 'native'
          ? nativeSwap
          : null,
      ],
    },
  };

  return wasmQuery(contractAddress, query);
};
