import React from 'react';
import { useApp } from '../../context/AppContext';
import textContext from '../../textContext';
import { useLocation, useNavigate } from 'react-router-dom';
import { anchor, terra, ust } from '../../assets/images/icons';

// Comps
// import Filter from '../layout/Filter';
import TableAssets from './TableAssets';
import TableLoader from './TableLoader';
import Asset from '../../pages/Asset';

// Material
import Grid from '@mui/material/Grid';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';

const Table = (props) => {
  const [icon, setIcon] = React.useState({});
  const [select, setSelect] = React.useState();

  const handleClick = (s) => {
    setSelect(s);
    icon.type === s
      ? setIcon({ type: s, asc: !icon.asc })
      : setIcon({ type: s, asc: false });
    select === s ? props.reverse() : props.click(s);
  };

  return (
    <Grid className="tableItems" container>
      <Grid item xs={6}>
        <p onClick={() => handleClick('pool')}>
          {props.text[0].text}{' '}
          <span
            className={`${
              icon.type === 'pool' && icon.asc ? 'active' : ''
            } icon`}
          >
            &#x25bc;
          </span>
        </p>
      </Grid>
      {
        <Grid item sx={{ paddingLeft: '.5rem' }} xs={3}>
          <p onClick={() => handleClick('tvl')}>
            {props.text[1].text}{' '}
            <span
              className={`${
                icon.type === 'tvl' && icon.asc ? 'active' : ''
              } icon`}
            >
              &#x25bc;
            </span>
          </p>
        </Grid>
      }
      <Grid item sx={{ paddingLeft: '.5rem' }} xs={2}>
        <p onClick={() => handleClick('APR')}>
          {props.text[2].text}{' '}
          <span
            className={`${
              icon.type === 'APR' && icon.asc ? 'active' : ''
            } icon`}
          >
            &#x25bc;
          </span>
        </p>
      </Grid>
      <Grid item xs={1}>
        <p>{props.text[3].text}</p>
      </Grid>
    </Grid>
  );
};

export default function Invest(props) {
  const [expand, setExpand] = React.useState(false);
  const [show, setShow] = React.useState(false);
  const [sort, setSort] = React.useState();
  const [reverse, setReverse] = React.useState(false);
  const { language, width } = useApp();
  const text = textContext[language].investment.invest;
  const [open, setOpen] = React.useState(false);
  const [isOpen, setIsOpen] = React.useState(false);
  const [def, setDefault] = React.useState();

  const { plus, data, search } = props;

  React.useEffect(() => {
    data && data.length !== 0 && setExpand(true);
  }, [data]);

  const handleExpand = () => {
    setExpand(!expand);
  };

  const handleClose = () => {
    setOpen(false);
    navigate('/strategy');
  };

  const { pathname } = useLocation();
  const navigate = useNavigate();

  React.useEffect(() => {
    const checker = (val) => {
      let newVal = val.split('&=');
      // default, setDefault
      let fil = data.filter((d) => d.address === newVal[1]);
      setDefault(fil[0]);
      if (newVal.length === 2 && fil.length !== 0) {
        !isOpen && setOpen(true);
      }
    };
    pathname.includes('/strategy/asset') && checker(pathname);
  }, [data]); // eslint-disable-line react-hooks/exhaustive-deps

  const handleSort = (a, b) => {
    let getA = !reverse ? a : b;
    let getB = !reverse ? b : a;
    let poolSort = () => {
      return getA.title < getB.title ? -1 : getA.title > getB.title ? 1 : 0;
    };
    let tvlSort = () => getB.tvl - getA.tvl;
    let aprSort = () => getB.APR - getA.APR;
    let actionSort = () => b.action - a.action;
    switch (sort) {
      case 'pool':
        return poolSort();
      case 'APR':
        return aprSort();
      case 'tvl':
        return tvlSort();
      case 'action':
        return actionSort();
      default:
        return poolSort();
    }
  };

  const loaders = new Array(6).fill('');

  const handleShow = () => setShow(!show);
  const handleSlice = !show ? 8 : undefined;
  return (
    <React.Fragment>
      <div className="investWrapper">
        <div className="wrapper">
          <Accordion defaultExpanded>
            <AccordionSummary
              expandIcon={<ExpandMoreIcon className="expandSVG" />}
              id="invest-header"
              onClick={handleExpand}
            >
              <p className="title">{props.title}</p>
            </AccordionSummary>
            <hr className="mobileHR" />
            <AccordionDetails className="investDetails">
              {/*<Filter filters={props.filters} />*/}
              <div className="investTable">
                <div className="tableHeaders">
                  {width > 1199 && (
                    <Table
                      reverse={() => setReverse(!reverse)}
                      click={setSort}
                      plus={plus}
                      text={text.order}
                    />
                  )}
                  {data && data.length !== 0
                    ? data
                        .sort(handleSort)
                        .filter((fil) =>
                          fil.title.toLowerCase().includes(search.toLowerCase())
                        )
                        .map((item, i) => {
                          return (
                            <TableAssets
                              isOpen={setIsOpen}
                              all={data}
                              data={item}
                              key={i}
                              plus={plus}
                            />
                          );
                        })
                        .slice(0, handleSlice)
                    : loaders.map((d, i) => <TableLoader d={d} key={i} />)}
                </div>
              </div>
              <div onClick={handleShow} className="accordionShow">
                {show ? text.hide : text.show}{' '}
                {show ? <ExpandLessIcon /> : <ExpandMoreIcon />}
              </div>
            </AccordionDetails>
          </Accordion>
        </div>
      </div>
      {open && (
        <Asset
          invest
          data={def}
          img1={terra}
          img2={anchor}
          img={ust}
          plus={props.plus}
          token={def.address}
          close={handleClose}
        />
      )}
    </React.Fragment>
  );
}
