import React from 'react';
import { useApp } from '../../context/AppContext';
import textContext from '../../textContext';
import Checkbox from '@mui/material/Checkbox';

export default function Disclaimer() {
  const { language, setAccept, getAccept } = useApp();
  const text = textContext[language].disclaimer;
  const [checked, setChecked] = React.useState(false);
  const [state, setState] = React.useState(false);

  const handleChange = (event) => {
    setChecked(!checked);
  };

  const handleAccept = () => {
    setAccept();
    document.body.style.overflow = 'auto';
    setState(true);
  };

  React.useEffect(() => {
    if (!getAccept) {
      document.body.style.overflow = 'hidden';
    }
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <React.Fragment>
      {!getAccept && !state && (
        <div id="disclaimer">
          <div className="wrapper">
            <h2>{text.header}</h2>
            <div className="text">
              <p>
                {text.text1}
                <a className="linkClass" href="/terms">
                  {text.text2}
                </a>
                {text.text3}{' '}
                <a
                  href={text.link.link}
                  className="linkClass"
                  target="_blank"
                  rel="noreferrer"
                >
                  {text.link.text}
                </a>{' '}
              </p>
            </div>
            <div className="confirm">
              <div className="flex" onClick={handleChange}>
                <Checkbox
                  size="small"
                  checked={checked}
                  inputProps={{ 'aria-label': 'controlled' }}
                />
                <small>{text.acknowledge}</small>
              </div>
              <button
                onClick={handleAccept}
                disabled={!checked}
                className={!checked ? 'buttonColor disabled' : 'buttonColor'}
              >
                {text.confirm}
              </button>
            </div>
          </div>
        </div>
      )}
    </React.Fragment>
  );
}
